import API from './API';

class APIs {

    constructor(correo,clave) {
        this.correo = correo || localStorage.getItem('Login')
        this.clave = clave || localStorage.getItem('Password')
    }
    Estatus() {
        return API({
                action:  null
            }).then(res => res.data);
	}

    Ini(accion) {
        return API({
                action:  accion,
                correo:  this.correo,
                clave:  this.clave
            }).then(res => res.data);
	}

    Procesar(accion,array) {
        return API({
                action:  accion,
                correo:  this.correo,
                clave:  this.clave,
                array:  array
            }).then(res => res.data);
	}


}


export default APIs;