<template>
  <div class="content">

    <div class="md-layout-item md-medium-size-100 md-size-100">
      <md-card>
        <md-card-header data-background-color="blue">
          <h4 class="title">Solicitudes Pendientes</h4>
          <p class="category">Total de Usuario: {{ solicitudes.length }}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="solicitudes">
            <md-table-row slot="md-table-row" slot-scope="{ item, index }">
              <md-table-cell md-label="N¬∞">{{ index + 1 }}</md-table-cell>
              <md-table-cell md-label="Fecha">{{ item.fecha }}</md-table-cell>
              <md-table-cell md-label="Trading">{{ item.titulo }} ({{ item.fecha_reg }})</md-table-cell>
              <md-table-cell md-label="Capital $">{{ item.capital }}</md-table-cell>
              <md-table-cell md-label="Monto $">{{ item.monto }}</md-table-cell>
              <md-table-cell md-label="D√≠as">{{ item.dias }}</md-table-cell>
              <md-table-cell md-label="Estatus">{{ estatus[item.estado] }}</md-table-cell>
              <md-table-cell md-label="Procesar">
                <md-list-item @click="Procesar(index)">
                  <i class="material-icons">done_all</i>
                </md-list-item>
              </md-table-cell>
            </md-table-row>
          </md-table>
        </md-card-content>
      </md-card>
    </div>

  </div>
</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";

export default {
  data() {
    return {
      indice: 0,
      solicitudes: [],
      estatus: ['Negada','En Proceso','Aprobada'],
    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      const Consulta = new API();
      Consulta.Ini("solicitudes").then(response => {
        this.solicitudes = response.solicitudes;
      });
    },
    Procesar(index) {
      const UPD = new API();
      UPD.Procesar("retiro_capital",{
        solic: this.solicitudes[index],
      }).then(response => {
        if (response.error){
          this.$fire({title:response.message, type:"error", timer:2000});
          //this.$notify(Mensaje(3,response.message));
        } else {
          this.$fire({title:response.message, type:"success", timer:2000});
          //this.$notify(Mensaje(0,response.message));
          this.Mostrar();
        }
      });
    },
  }
};
</script>
