<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT();

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      mercado: 0,
      porcentaje: 0,
      comision: 0,
      retiro_antic: 0,
      retiro_flat: 0,
      wallet: null,
      balance: 0,
      WIF: null,
      address: {
        proveedor: "-",
        comision: "-",
        servicio: "-",
        salida: "-"
      },
      Hex: null,
      disabled0: true,
      disabled1: true,
      disabled2: true,
      disabled3: true,
      disabled4: true,
      disabled5: true,
      hash: null,
      valor: null,
      transactions: 0,
      ver: 0,
      distribucion0: [],
      distribucion1: [],
      correo: null,
      stripe: null,
      clave: null,

    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      this.wallet = null;
      this.balance = 0;
      this.porcentaje = 0;
      this.comision = 0;
      this.retiro_antic = 0;
      this.retiro_flat = 0;
      this.WIF = null;
      this.address = {};
      this.Hex = null;
      this.disabled0 = true;
      this.disabled1 = true;
      this.disabled2 = true;
      this.disabled3 = true;
      this.disabled4 = true;
      this.disabled5 = true;
      this.hash = null;
      this.valor = null;
      this.distribucion0 = [];
      this.distribucion1 = [];
      this.transactions = 0;
      this.ver = 0;
      this.utxos_disp = 0;
      const Consulta = new API();
      Consulta.Ini('configuracion').then(response => {
  
        this.porcentaje = response.configuracion.price_spread;
        this.comision = response.configuracion.comision;
        this.retiro_antic = response.configuracion.retiro_antic;
        this.retiro_flat = response.configuracion.retiro_flat;
        const decWIF = secure.decrypt(response.configuracion.proveedor_wif,"btcoin");
        this.correo = response.configuracion.correo;
        this.clave = response.configuracion.pass_correo;
        this.stripe = secure.decrypt(response.configuracion.pubKey_stripe,"btcoin");
        this.WIF = decWIF;
        this.address.proveedor = response.configuracion.proveedor_address;
        this.address.comision = response.configuracion.comision_address;
        this.address.servicio = response.configuracion.servicio_address;
        this.address.salida = response.configuracion.salida_address;
        Bitcoin.privateKeyToAccount(decWIF);
        this.wallet = Bitcoin.defaultAccount.address;
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat(result.bpi.USD.rate.replace(",", "")).toFixed(2);
          this.mercado = btc;
        });
        Bitcoin.getBalance().then(result => {
          this.transactions =
            result.chain_stats["tx_count"] + result.mempool_stats["tx_count"];
          const received = result.chain_stats["funded_txo_sum"];
          const spent = result.chain_stats["spent_txo_sum"];
          this.balance = ((received - spent) / 100000000).toFixed(8);
        });
        Consulta.Ini('distribucion').then(response => {
          this.distribucion0 = response.distribucion['inversion'];
          this.distribucion1 = response.distribucion['ganancias'];
        });
      });
    },
    Editar(caso) {
      if (caso == 0) {
        this.disabled0 = false;
      }
      if (caso == 1) {
        this.disabled1 = false;
      }
      if (caso == 2) {
        this.disabled2 = false;
      }
      if (caso == 3) {
        this.disabled3 = false;
      }
      if (caso == 4) {
        this.disabled4 = false;
      }
      if (caso == 5) {
        this.disabled5 = false;
      }
    },
    Guardar(caso) {
      if (!Bitcoin.isValidAddress(this.address.servicio)) {
        this.$fire({title:"Address de Servicio Inv√°lida.", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "Address de Servicio Inv√°lida."));
      } else if (!Bitcoin.isValidAddress(this.address.comision)) {
        this.$fire({title:"Address de Servicio Inv√°lida.", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "Address de Servicio Inv√°lida."));
      } else if (!Bitcoin.isValidAddress(this.address.proveedor)) {
        this.$fire({title:"Address de Proveedor Inv√°lida.", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "Address de Proveedor Inv√°lida."));
      } else if (this.address.proveedor != this.wallet) {
        this.$fire({title:"Private Key de Proveedor Inv√°lida", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "Private Key de Proveedor Inv√°lida"));
      } else {
        if (caso == 2) {
          let sumap = 0;
          for (let i = 0; i < this.distribucion0.length; i++) {
                sumap += parseFloat(this.distribucion0[i].porcentaje);
          }
          if (sumap == 100){
            for (let i = 0; i < this.distribucion0.length; i++) {
              const UPD = new API();
              UPD.Procesar("updatedistribucion",{
                id: this.distribucion0[i].id,
                porcentaje: this.distribucion0[i].porcentaje
              }).then(response => {
                if (response.error) {
                  this.$fire({title:response.message,type:"error",timer:2000});
                  //this.$notify(Mensaje(3, response.message));
                } else {
                  this.$fire({title:response.message, type:"success", timer:2000});
                  //this.$notify(Mensaje(0, response.message));
                  this.distribucion0 = [];
                  this.Mostrar();
                }
              });
            }
          } else {
            this.$fire({title:"La suma de la Distribuci√≥n debe ser igual a 100% ", type:"error", timer:2000});
            //this.$notify(Mensaje(3, "La suma de la Distribuci√≥n debe ser igual a 100% "));
            this.Mostrar();
          }
        } else if (caso == 5) {
          let sumap = 0;
          for (let i = 0; i < this.distribucion1.length; i++) {
                sumap += parseFloat(this.distribucion1[i].porcentaje);
          }
          if (sumap == 100){
            for (let i = 0; i < this.distribucion1.length; i++) {
              const UPD = new API();
              UPD.Procesar("updatedistribucion",{
                id: this.distribucion1[i].id,
                porcentaje: this.distribucion1[i].porcentaje
              }).then(response => {
                if (response.error) {
                  this.$fire({title:response.message,type:"error",timer:2000});
                  //this.$notify(Mensaje(3, response.message));
                } else {
                  this.$fire({title:response.message, type:"success", timer:2000});
                  //this.$notify(Mensaje(0, response.message));
                  this.distribucion1 = [];
                  this.Mostrar();
                }
              });
            }
          } else {
            this.$fire({title:"La suma de la Distribuci√≥n debe ser igual a 100% ", type:"error", timer:2000});
            //this.$notify(Mensaje(3, "La suma de la Distribuci√≥n debe ser igual a 100% "));
            this.Mostrar();
          }
        } else {
          const UPD = new API();
          UPD.Procesar("updateconfiguracion",{
            price_spread: this.porcentaje,
            comision: this.comision,
            retiro_antic: this.retiro_antic,
            retiro_flat: this.retiro_flat,
            proveedor_address: this.address.proveedor,
            proveedor_wif: secure.encrypt(this.WIF, "btcoin"),
            servicio_address: this.address.servicio,
            comision_address: this.address.comision,
            salida_address: this.address.salida,
            correo: this.correo,
            pass_correo: this.clave,
            pubKey_stripe: secure.encrypt(this.stripe, "btcoin"),
          }).then(response => {
            if (response.error) {
              this.$fire({title:response.message, type:"error", timer:2000});
              //this.$notify(Mensaje(3, response.message));
              this.Mostrar();
            } else {
              this.$fire({title:response.message, type:"success", timer:2000});
              //this.$notify(Mensaje(0, response.message));
            }
          }); 
        }
        if (caso == 0) {
          this.disabled0 = true;
        }
        if (caso == 1) {
          this.disabled1 = true;
        }
        if (caso == 2) {
          this.disabled2 = true;
        }
        if (caso == 3) {
          this.disabled3 = true;
        }
        if (caso == 4) {
          this.disabled4 = true;
        }
        if (caso == 5) {
          this.disabled5 = true;
        }
      }
    },
    Cerrar(caso) {
      if (caso == 0) {
        this.disabled0 = true;
      }
      if (caso == 1) {
        this.disabled1 = true;
      }
      if (caso == 2) {
        this.disabled2 = true;
      }
      if (caso == 3) {
        this.disabled3 = true;
      }
      if (caso == 4) {
        this.disabled4 = true;
      }
      if (caso == 5) {
        this.disabled5 = true;
      }
    }
  }
};
</script>
