<template>
  <div class="content">
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-20 md-size-20"></div>
      <div class="md-layout-item md-medium-size-60 md-size-60" v-if="ingresar">
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">
              <i class="pi pi-user" style="fontSize: 1rem"></i>
              Login
            </h4>
            <p class="category">Complete para ingresar</p>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-100 md-medium-size-100">
                <md-field>
                  <label>Correo</label>
                  <md-input v-model="correo" type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100 md-medium-size-100">
                <md-field>
                  <label>Clave</label>
                  <md-input v-model="clave" type="password" @keyup.enter="Ingresar"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-100 md-medium-size-100 text-right">
                <md-button
                  class="md-raised md-warning"
                  @click.native.prevent="Recuperar"
                >
                  Recuperar Clave</md-button
                >
                <md-button
                  class="md-raised md-success"
                  @click.native.prevent="Ingresar"
                >
                  Ingresar</md-button
                >
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>
      <div class="md-layout-item md-medium-size-60 md-size-60" v-else>
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title">
              <i class="pi pi-user" style="fontSize: 1rem"></i>
              Recuperar Clave
            </h4>
            <p class="category">Complete los datos solicitados</p>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-100 md-medium-size-100">
                <md-field>
                  <label>Correo</label>
                  <md-input v-model="correo" type="email" :disabled="process == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50 md-medium-size-50" v-if="process">
                <md-field>
                  <label>Clave</label>
                  <md-input v-model="clave1" type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50 md-medium-size-50" v-if="process">
                <md-field>
                  <label>Confirmar Clave</label>
                  <md-input v-model="clave2" type="password"></md-input>
                </md-field>
              </div>
               <div class="md-layout-item md-size-100 md-medium-size-100" v-if="process">
                <div class="alert alert-warning">
                  <span>
                    La clave debe tener una longitud m√≠nima de 8, may√∫sculas,
                    min√≠sculas y un car√°cter especial ! @ # $ % ^ & * _ .
                  </span>
                </div>
              </div>
             <div class="md-layout-item md-size-100 md-medium-size-100 text-right">
                <md-button
                  class="md-raised md-warning"
                  @click.native.prevent="Recuperar"
                >
                  Regresar</md-button
                >
                <md-button
                  class="md-raised md-success"
                  @click.native.prevent="Procesar(1)"
                  v-if="!process"
                >
                  Verificar</md-button
                >
                <md-button
                  class="md-raised md-success"
                  @click.native.prevent="Procesar(2)"
                  v-else
                >
                  Procesar</md-button
                >
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import Crypto from "crypto-js";

export default {
  data() {
    return {
      correo: null,
      clave: null,
      codigo: null,
      clave1: null,
      clave2: null,
      validar: false,
      ingresar: true,
      process: false
    };
  },
  created() {
    this.$store.commit('Logout');
    this.Mostrar();
  },
  methods: {
    Mostrar() {
      this.process = false;
      this.clave = null;
      this.clave1 = null;
      this.clave2 = null;
      this.validar = false;
    },
    Ingresar() {
      if (this.correo == null || this.clave == null) {
        this.$fire({title:"Debe ingresar los datos solicitados.", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "Debe ingresar los datos solicitados."));
      } else if (
        !/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          this.correo
        )
      ) {
        this.$fire({title:"El correo no es v√°lido", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "El correo no es v√°lido"));
      } else {
        this.validar = true;
      }
      if (this.validar) {
        const cifrado = Crypto.HmacSHA1(this.clave, "btcoin").toString();
        const Consulta = new API(this.correo,cifrado);
		    Consulta.Ini('login').then(response => {
     		  if (response.authenticated) {
					  this.$store.commit('Seguridad',{
							correo: this.correo, 
							clave: cifrado, 
							usuario: response.user.nombre + ' ' + response.user.apellido,
              rol: response.user.rol,
            });
 						this.$store.commit('Menu',{
						  rol: response.user.rol
            });
            this.$router.push('dashboard');
          } else {
            this.$fire({title:"Usuario o Clave incorrectas!", type:"error", timer:2000});
            //this.$notify(Mensaje(3, 'Usuario o Clave incorrectas!'));
          }
        }); 
      }
    },
    Recuperar() {
      this.ingresar = !this.ingresar;
      this.Mostrar();
    },
    Procesar(caso) {
      if (caso == 1){
        if (this.correo == null) {
          this.$fire({title:"Debe ingresar los datos solicitados.", type:"error", timer:2000});
          //this.$notify(Mensaje(3, "Debe ingresar los datos solicitados."));
        } else if (
          !/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          this.correo
          )
        ) {
          this.$fire({title:"El correo no es v√°lido", type:"error", timer:2000});
          //this.$notify(Mensaje(3, "El correo no es v√°lido"));
        } else {
          const Consulta = new API(this.correo,0);
		      Consulta.Ini('verifcorreo').then(response => {
     		    if (response.authenticated) {
              this.$prompt("Ingrese el C√≥digo de Seguridad, que fue enviado a su correo",
                "","Verificaci√≥n","question").then(text => {
                  if (text == response.codigo){
                    this.$fire({title:'V√°lido', type:"success", timer:2000});
                    this.codigo = response.codigo;
                    this.process = true;
                  } else {
                    this.$fire({title:'C√≥digo Inv√°lido', type:"error", timer:2000});
                  }
              });
            } else {
              this.$fire({title:"El Correo no aparece registrado, verifique", type:"error", timer:2000});
              //this.$notify(Mensaje(3, 'El Correo no aparece registrado, verifique'));
            }
          }); 
        }
      }
      if (caso == 2){
        if (this.correo == null || this.clave1 == null || this.clave2 == null) {
          this.$fire({title:"Debe ingresar los datos solicitados.", type:"error", timer:2000});
          //this.$notify(Mensaje(3, "Debe ingresar los datos solicitados."));
        } else if (
          !/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[_.!@#\$%\^&\*])(?=.{8,})/.test(
            this.clave1
          )) {
          this.$fire({title:"La clave debe tener una longitud m√≠nima de 8, may√∫sculas, min√≠sculas y un car√°cter especial", type:"error", timer:2000});
          //this.$notify(Mensaje(3,"La clave debe tener una longitud m√≠nima de 8, may√∫sculas, min√≠sculas y un car√°cter especial"));
        } else if (this.clave1 != this.clave2) {
          this.$fire({title:"La confirmaci√≥n de clave es incorrecta", type:"error", timer:2000});
          //this.$notify(Mensaje(3, "La confirmaci√≥n de clave es incorrecta"));
        } else {
          const cifrado = Crypto.HmacSHA1(this.clave1, "btcoin").toString();
          const UPD = new API(this.correo,this.codigo);
          UPD.Procesar("updateclave",{
            clave: cifrado,
          }).then(response => {
            if (response.error) {
              this.$fire({title:response.message, type:"error", timer:2000});
              //this.$notify(Mensaje(3, response.message));
            } else {
              this.$fire({title:response.message, type:"success", timer:2000});
              //this.$notify(Mensaje(0, response.message));
              this.Mostrar();
              this.ingresar = !this.ingresar;
            }
          });
        }
      }
    },
  }
};
</script>
