<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },
  data() {
    return {
      tabla: 0,
      recibido: [],
      consol1: [],
      consol2: [],
      modificar: [],
      plan: [],
      total: [],
      activo: true,
      invertir: false,
      rentabil: false,
      capital: 0,
    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      this.capital = 0;
      this.tabla = 0;
      this.activo = true;
      this.invertir = false;
      this.rentabil = false;
      this.total = [0,0,0,0];
      this.modificar = [];
      this.recibido = [];
      this.consol1 = [];
      this.consol2 = [];
      this.plan = [];
      this.rentabilidad = [];
      const Consulta = new API();
      Consulta.Ini("consolidado").then(response => {
        this.capital= response.capital;
        if (response.total[0]>0){this.total[0] = response.total[0];} else {this.total[0] = 0;}
        if (response.total[1]>0){this.total[1] = response.total[1];} else {this.total[1] = 0;}
        if (response.total[2]>0){this.total[2] = response.total[2];} else {this.total[2] = 0;}
        if (response.total[3]>0){this.total[3] = response.total[3];} else {this.total[3] = 0;}
        this.recibido = response.recibido;
        this.consol1 = response.consol1;
        this.consol2 = response.consol2;
        this.rentabilidad = response.rentabilidad;
      });
    },
    Tabla(index) {
      if (this.tabla != index) {
        this.tabla = index;
      } else {
        this.tabla = 0;
      }
    },
    Consolidar(index) {
      const UPD = new API();
      UPD.Procesar("consolidar",{
        fecha: this.recibido[index]['fecha'],
        monto: this.recibido[index]['monto'],
      }).then(response => {
        if (response.error){
          this.$fire({title:response.message, type:"error", timer:2000});
          //this.$notify(Mensaje(3,response.message));
        } else {
          this.$fire({title:response.message, type:"success", timer:2000});
          //this.$notify(Mensaje(0,response.message));
          this.Mostrar();
        }
      });
    },
    Procesar(caso,index) {
      this.tabla = 0;
      this.activo = false;
      if (caso == 1){
        this.invertir = true;
        this.modificar = this.consol1[index];
        this.modificar.fecha_inv = this.consol1[index].fecha;
        this.modificar.destino = null;
        this.modificar.observacion = null;
        this.modificar.dias = 1;
      }
      if (caso == 2){
        this.plan = [];
        this.rentabil = true;
        this.modificar = this.consol2[index];
      }
    },
    Cerrar() {
      this.tabla = 0;
      this.activo = true;
      this.modificar = [];
      this.invertir = false;
      this.rentabil = false;
    },
    Plan(id,fecha) {
      this.plan = [];
      const UPD = new API();
      UPD.Procesar("plan",{
        id: id,
        fecha: fecha,
      }).then(response => {
        this.plan =response.plan;
        if(response.plan == null){
          this.$notify(Mensaje(3,'Ya est√° cargada la rentabilidad de esa fecha'));
        }
      });
    },
    Invertir(id,index) {
      const UPD = new API();
      UPD.Procesar("consolidar_invertir",{
        id: this.modificar.dist[index].id_dist,
        fecha_inv: this.modificar.dist[index].fecha_inv,
        destino: this.modificar.destino,
        observacion: this.modificar.observacion,
        estado: 2,
      }).then(response => {
        if (response.error){
          this.$fire({title:response.message, type:"error", timer:2000});
          //this.$notify(Mensaje(3,response.message));
        } else {
          this.$fire({title:response.message, type:"success", timer:2000});
          //this.$notify(Mensaje(0,response.message));
          this.Mostrar();
        }
      });
    },
    Guardar(caso) {
      const UPD = new API();
      if (caso == 1){
        UPD.Procesar("consolidar_invertir",{
          id: this.modificar.id,
          fecha_inv: this.modificar.fecha_inv,
          destino: this.modificar.destino,
          observacion: this.modificar.observacion,
          estado: 2,
        }).then(response => {
          if (response.error){
            this.$fire({title:response.message, type:"error", timer:2000});
            //this.$notify(Mensaje(3,response.message));
          } else {
            this.$fire({title:response.message, type:"success", timer:2000});
            //this.$notify(Mensaje(0,response.message));
            this.Mostrar();
          }
        });
      }
      if (caso == 2){
        UPD.Procesar("cargar_rentabilidad",{
          consolidado: this.modificar.id,
          fecha: this.modificar.fecha_rent,
          dias: this.modificar.dias,
          rent: this.plan,
        }).then(response => {
          if (response.error){
            this.$fire({title:response.message, type:"error", timer:2000});
            //this.$notify(Mensaje(3,response.message));
          } else {
            this.$fire({title:response.message, type:"success", timer:2000});
            //this.$notify(Mensaje(0,response.message));
            this.Mostrar();
          }
        });
      }
    },
    Distruibuir(index) {
      const UPD = new API();
      UPD.Procesar("distruibuir_rentabilidad",{
        consolidado: this.rentabilidad[index].consolidado,
        fecha: this.rentabilidad[index].fecha,
        dias: this.rentabilidad[index].dias,
        estado: 2,
      }).then(response => {
        if (response.error){
          this.$fire({title:response.message, type:"error", timer:2000});
          //this.$notify(Mensaje(3,response.message));
        } else {
          this.$fire({title:response.message, type:"success", timer:2000});
          //this.$notify(Mensaje(0,response.message));
          this.Mostrar();
        }
      });
    },
    convfech(fecha){
      return fecha.replace(/^(\d{4})-(\d{2})-(\d{2})$/g,'$3/$2/$1');
    },
  }
};
</script>
