<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT();

import { StatsCard, ChartCard } from "@/components";

export default {
  components: {
    StatsCard,
    ChartCard
  },

  data() {
    return {
      wallet: null,
      reporte: [],
      dashboard: [],
      saldo: 0,
      balance: 0,
      mercado: 0,
      ver: 0,
      detalle_inversiones: [],
      detalle_rentabilidad: [],
      mes: ["Ene","Feb","Mar","Abr","May","Jun","Jul","Ago","Sep","Oct","Nov","Dic"],
      options: {},
      color: ["#26c6da", "red", "orange", "purple", "green", "gray"],
      usuariosChart: {
        labels: [],
        series: []
      },
      portafolioChart: {
        labels: [],
        series: []
      },
      intercambioChart: {
        labels: [],
        series: []
      },
      intercambio2Chart: {
        labels: [],
        series: []
      },
      inversionesChart: {
        labels: [],
        series: [],
        plan: []
      },
      estdinverChart: {
        data: {
          labels: [],
          series: []
        },
        options: {
          lineSmooth: this.$Chartist.Interpolation.cardinal({
            tension: 0
          }),
          low: 0,
          high: 100000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
          chartPadding: {
            top: 0,
            right: 0,
            bottom: 0,
            left: 20
          }
        }
      },

      dailySalesChart: {
        data: {
          labels: [],
          series: []
        },
        options: {
          lineSmooth: this.$Chartist.Interpolation.cardinal({
            tension: 0
          }),
          low: 0,
          high: 50, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
          chartPadding: {
            top: 0,
            right: 0,
            bottom: 0,
            left: 0
          }
        }
      }
    };
  },
  created() {

    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      this.saldo = 0;
      this.balance = 0;
      this.mercado = 0;
      this.wallet = null;
      this.ver = 0;
      this.detalle_inversiones = [];
      this.detalle_rentabilidad = [];
      const Consulta = new API();
      Consulta.Ini('informacion').then(response => {

        this.reporte = response;
        const user = response.user;

        if (this.$store.state.rol==2) {
          
          Consulta.Ini('dashboard').then(response => {
            this.dashboard = response;
            this.detalle_inversiones = response.detalle_inversiones;
        
            this.portafolioChart.labels.push(
              (
                (response.inversion_ejecucion * 100) /
                response.inversion_total
              ).toFixed(0) + "%"
            );
            this.portafolioChart.series.push(response.inversion_ejecucion);
            this.portafolioChart.labels.push(
              (
                (response.inversion_cerrada * 100) /
                response.inversion_total
              ).toFixed(0) + "%"
            );
            this.portafolioChart.series.push(response.inversion_cerrada);
            this.portafolioChart.labels.push(
              (
                (response.inversion_completada * 100) /
                response.inversion_total
              ).toFixed(0) + "%"
            );
            this.portafolioChart.series.push(
              response.inversion_completada
            );
            this.usuariosChart.labels.push(
              (
                (response.user_admin * 100) /
                response.user_total
              ).toFixed(0) + "%"
            );

            this.usuariosChart.series.push(response.user_admin);
            this.usuariosChart.labels.push(
              (
                (response.user_inactivo * 100) /
                response.user_total
              ).toFixed(0) + "%"
            );
            this.usuariosChart.series.push(response.user_inactivo);
            this.usuariosChart.labels.push(
              (
                (response.user_basico * 100) /
                response.user_total
              ).toFixed(0) + "%"
            );
            this.usuariosChart.series.push(response.user_basico);

            this.intercambioChart.labels.push(
              (
                (response.interc_USDUSD.operaciones * 100) /
                response.interc_TOTAL
              ).toFixed(0) + "%"
            );
            this.intercambioChart.series.push(
              response.interc_USDUSD.operaciones
            );
            this.intercambioChart.labels.push(
              (
                (response.interc_USDBTC.operaciones * 100) /
                response.interc_TOTAL
              ).toFixed(0) + "%"
            );
            this.intercambioChart.series.push(
              response.interc_USDBTC.operaciones
            );

            this.intercambio2Chart.labels.push(
              "$" + parseFloat(response.interc_USDUSD.monto).toFixed(2)
            );
            this.intercambio2Chart.series.push(
              response.interc_USDUSD.monto
            );
            this.intercambio2Chart.labels.push(
              "$" + parseFloat(response.interc_USDBTC.monto).toFixed(2)
            );
            this.intercambio2Chart.series.push(
              response.interc_USDBTC.monto
            );

            this.estdinverChart.data.labels = response.interc_estad.reverse();
            this.estdinverChart.data.series.push(
              response.interc_estadUSDUSD.reverse()
            );
            this.estdinverChart.data.series.push(
              response.interc_estadUSDBTC.reverse()
            );
            
            const max1 = Math.max.apply(null, response.interc_estadUSDUSD);
            const max2 = Math.max.apply(null, response.interc_estadUSDBTC);
            this.estdinverChart.options.high = Math.max(max1, max2);
          });

        } else {

          this.detalle_inversiones = response.detalle_inversiones;

          if (response.detalle_rentabilidad) {
            response.detalle_rentabilidad.forEach(element => {
              if (element.tipo>0) {
                this.detalle_rentabilidad.push({
                  id: element.id,
                  fecha: element.fecha,
                  titulo: element.titulo,
                  credito: '$'+parseFloat(element.monto).toFixed(4),
                  debito: 0,
                  descripcion: element.descripcion
                });
              } else {
                this.detalle_rentabilidad.push({
                  id: element.id,
                  fecha: element.fecha,
                  titulo: element.titulo,
                  credito: 0,
                  debito: '$'+parseFloat(element.monto).toFixed(4),
                  descripcion: element.descripcion
                });
              }
            });
          }
          if (response.estad_mirenta_meses) {
            this.dailySalesChart.data.labels = response.estad_mirenta_meses.reverse();           
            this.dailySalesChart.data.series.push(response.estad_mirenta_monto.reverse());
            const max1 = Math.max.apply(null, response.estad_mirenta_monto);
            const max2 = Math.max.apply(null, response.estad_mirenta_monto);
            this.dailySalesChart.options.high = Math.max(max1, max2);
          } 
          if (response.mis_inversiones) {
            response.mis_inversiones.forEach(element => {
              this.inversionesChart.labels.push(
                "$" + parseFloat(element.total).toFixed(0)
              );
              this.inversionesChart.series.push(element.total);
              this.inversionesChart.plan.push(element.titulo);
            });
          }
        }
      }); 

    },
    Ver(caso) {
      if (this.ver == caso) {
        this.ver = 0;
      } else {
        this.ver = caso;
      }
    }
  }
};
</script>
