</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";

import {} from "@/components";

export default {
  components: {},
  data() {
    return {
      correo: "",
      clave: "",
      correo_reg: "",
      clave_reg: "",
      clave_reg2: "",
      saldo: 0,
      autenticado: false,
      estatus: false,
      statsCards: [],
      icono: ["trending_up", "done_all", "lock"],
      etapa: ["En Proceso", "Completo", "Cerrado"],
      tipo: ["orange", "blue", "red"],
      reporte: []
    };
  },
  created() {
    this.Mostrar();
  },
  methods: {
    Mostrar() {
      this.reporte = [];
      API({
        action: "informacion",
        correo: localStorage.getItem("Login"),
        clave: localStorage.getItem("Password")
      }).then(response => {
        this.saldo = parseFloat(response.data.saldo).toFixed(2);
        this.reporte[0] = response.data.inversion_activas;
        this.reporte[1] = response.data.inversion_ejecucion;
        this.reporte[2] = 0;
        this.reporte[3] = response.data.inversionistas;
      });
      API({
        action: "marketplace",
        correo: localStorage.getItem("Login"),
        clave: localStorage.getItem("Password")
      }).then(response => {
        response.data.portafolio.forEach(element => {
          this.statsCards.push({
            id: element.id,
            etapa: element.etapa,
            icon: this.icono[element.etapa],
            type: this.tipo[element.etapa],
            etap: this.etapa[element.etapa],
            title: element.titulo,
            descr: element.descripcion,
            value: element.meta,
            monto: element.monto,
            invertir: 25,
            footerIni: "Fecha de Inicio: " + element.fecha_ini,
            footerFin: "Fecha tope: " + element.fecha_fin,
            invers: element.invers,
            porcentaje: Math.round((element.monto / element.meta) * 100),
            footerIcon: "ti-time"
          });
        });
      });
    },
    Invertir(index) {
      if (!this.statsCards[index].invertir) {
        this.$notify(Mensaje(3, "Debe colocar el monto a invertir"));
      } else {
        API({
          action: "invertir",
          correo: localStorage.getItem("Login"),
          clave: localStorage.getItem("Password"),
          portaf: this.statsCards[index].id,
          monto: this.statsCards[index].invertir
        }).then(response => {
          if (response.data.error) {
            this.$notify(Mensaje(3, response.data.message));
          } else {
            this.$notify(Mensaje(2, response.data.message));
            this.statsCards = [];
            this.Mostrar();
          }
        });
      }
    }
  }
};
</script>
