<template>
  <div class="content">
    {{usuarios[1]}}
    <div class="md-layout-item md-medium-size-100 md-size-100">
      <md-card>
        <md-card-header data-background-color="green">
          <h4 class="title">Reportes</h4>
        </md-card-header>
        <md-card-content>
          <div class="md-layout">
            <div class="md-layout-item md-medium-size-40 md-size-40">
              <md-field>
              <label>Nombre del Reporte</label>
                <md-select v-model="selec">
                  <md-option v-for="(item, index) in reporte" :key="index" :value="index">
                    {{ item }}
                  </md-option>
                </md-select>
              </md-field>
            </div>
            <div class="md-layout-item md-medium-size-40 md-size-40" v-if="usuarios[selec]">
              <md-field>
              <label>Usuario</label>
                <md-select v-model="usuario">
                  <md-option v-for="(item, index) in usuarios[selec]" :key="index" :value="index">
                    {{ item }}
                  </md-option>
                </md-select>
              </md-field>
            </div>
            <div class="md-layout-item md-medium-size-20 md-size-20" v-if="usuarios[selec]">
              <md-button class="md-round  md-success" @click="Buscar(selec,usuario)">
                Mostrar</md-button>
            </div>
          </div>
        </md-card-content>
      </md-card>
    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="caso == 1">     
      <md-card>
        <md-card-header data-background-color="blue">
          <h4 class="title">Solicitudes de Capital</h4>
            <p class="category">
              Total de registros: {{ mostrar.length }}
            </p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="mostrar">
            <md-table-row slot="md-table-row" slot-scope="{ item, index }">
              <md-table-cell md-label="N¬∞">{{ index + 1 }}</md-table-cell>
              <md-table-cell md-label="Fecha">{{ item.fecha }}</md-table-cell>
              <md-table-cell md-label="Trading">{{ item.titulo }}</md-table-cell>
              <md-table-cell md-label="Capital $">{{ item.capital }}</md-table-cell>
              <md-table-cell md-label="Monto $">{{ item.monto }}</md-table-cell>
              <md-table-cell md-label="Estatus">{{ estatus[item.estado] }}</md-table-cell>
              <md-table-cell md-label="Usuario">{{ item.correo }}</md-table-cell>
            </md-table-row>
          </md-table>
        </md-card-content>
      </md-card>
    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="caso == 2">     
      <md-card>
        <md-card-header data-background-color="blue">
          <h4 class="title">Movimientos de Capital</h4>
            <p class="category">
              Total de registros: {{ mostrar.length }}
            </p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="mostrar">
            <md-table-row slot="md-table-row" slot-scope="{ item, index }">
              <md-table-cell md-label="N¬∞">{{ index + 1 }}</md-table-cell>
              <md-table-cell md-label="Fecha">{{ item.fecha }}</md-table-cell>
              <md-table-cell md-label="Descripci√≥n">{{ item.descripcion }}</md-table-cell>
              <md-table-cell md-label="Debito $" v-if="item.tipo>0">0</md-table-cell>
              <md-table-cell md-label="Debito $" v-else>{{ item.monto }}</md-table-cell>
              <md-table-cell md-label="Credito $" v-if="item.tipo>0">{{ item.monto }}</md-table-cell>
              <md-table-cell md-label="Credito $" v-else>0</md-table-cell>
              <md-table-cell md-label="Usuario">{{ item.correo }}</md-table-cell>
            </md-table-row>
          </md-table>
        </md-card-content>
      </md-card>
    </div>


  </div>
</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";

export default {
  data() {
    return {
      reporte: [
          'Seleccionar',
          'Solicitudes de Capital',
          'Movimientos de Capital'
      ],
      selec: 0,
      caso: 0,
      usuario: 0,
      usuarios: [],
      reportes: [],
      seleccion: [],
      mostrar: [],
      estatus: ['Negada','En Proceso','Aprobada'],
    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      const Consulta = new API();
      Consulta.Ini("reportes").then(response => {
        if (response.solicitudes) {
          this.reportes[1] = response.solicitudes;
          let data1 = ['Todos'];
          response.solicitudes.forEach(element => {
            data1.push(element.correo);
          });
          const dataArr1 = new Set(data1);
          data1 = [...dataArr1];        
          this.usuarios[1] = data1; 
        }
        if (response.capital) {
          this.reportes[2] = response.capital;
          let data2 = ['Todos'];
          response.capital.forEach(element => {
            data2.push(element.correo);
          });
          const dataArr2 = new Set(data2);
          data2 = [...dataArr2];        
          this.usuarios[2] = data2; 
        }




      });
    },
    Buscar(selec,usuario) {
      this.mostrar =[];
      if (usuario>0) {
        this.reportes[selec].forEach(element => {
          if (element.correo == this.usuarios[selec][usuario]){
            this.mostrar.push(element);
          }
        });
      } else {
        this.mostrar = this.reportes[selec];
      }
        this.caso = selec;
    },
  }  
};
</script>
