<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      reporte: [],
      portafolio: [],
      etapa: ["En Proceso", "Completo", "Cerrado"],
      modificar: {
        activo: false,
        id: 0,
        nombre: null,
        descripcion: null,
        fecha_ini: null,
        fecha_fin: null,
        estatus: null,
        objetivo: 0,
        rentabilidad: 0,
        etapa: 0,
        multiplo: 0
      }
    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      this.reporte = [];
      this.portafolio = [];
      const Consulta = new API();
      Consulta.Ini('dashboard').then(response => {
        this.reporte = response;
      });
      Consulta.Ini('portafolio').then(response => {
        this.portafolio = response.portafolio;
      });
    },
    Editar(index) {
      this.modificar.activo = true;
      this.modificar.id = this.portafolio[index].id;
      this.modificar.nombre = this.portafolio[index].titulo;
      this.modificar.descripcion = this.portafolio[index].descripcion;
      this.modificar.fecha_ini = this.portafolio[index].fecha_ini;
      this.modificar.fecha_fin = this.portafolio[index].fecha_fin;
      this.modificar.estatus = this.portafolio[index].estatus;
      this.modificar.objetivo = parseFloat(this.portafolio[index].meta).toFixed(2);
      this.modificar.rentabilidad = parseFloat(this.portafolio[index].rentabilidad).toFixed(2);
      this.modificar.etapa = this.portafolio[index].etapa;
      this.modificar.multiplo = this.portafolio[index].multiplo;
      this.Mostrar();
    },
    Guardar() {
      const UPD = new API();
      UPD.Procesar("updateportafolio",{
        id: this.modificar.id,
        titulo: this.modificar.nombre,
        descripcion: this.modificar.descripcion,
        fecha_ini: this.modificar.fecha_ini,
        fecha_fin: this.modificar.fecha_fin,
        meta: this.modificar.objetivo,
        etapa: this.modificar.etapa,
        multiplo: this.modificar.multiplo,
        rentabilidad: this.modificar.rentabilidad
      }).then(response => {
        if (response.error) {
          this.$fire({title:response.message, type:"error", timer:2000});
          //this.$notify(Mensaje(3, response.message));
        } else {
          this.$fire({title:response.message, type:"success", timer:2000});
          //this.$notify(Mensaje(0, response.message));
          this.portafolio = [];
          this.Mostrar();
        }
      });
    },
    Cerrar() {
      this.modificar.activo = false;
      this.modificar.id = 0;
      this.modificar.nombre = null;
      this.modificar.descripcion = null;
      this.modificar.fecha_ini = null;
      this.modificar.fecha_fin = null;
      this.modificar.estatus = null;
      this.modificar.objetivo = 0;
      this.modificar.etapa = 0;
      this.modificar.rentabilidad = 0;
    }
  }
};
</script>
