<template>
  <div class="content">
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-size-100">
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title">
              <i class="pi pi-user-plus" style="fontSize: 1rem"></i> Register
            </h4>
            <p class="category">Complete el registro de Usuario</p>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-50 md-medium-size-50">
                <md-field>
                  <label>Nombre</label>
                  <md-input v-model="nombre" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50 md-medium-size-50">
                <md-field>
                  <label>Apellido</label>
                  <md-input v-model="apellido" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100 md-medium-size-100">
                <md-field>
                  <label>Correo</label>
                  <md-input v-model="correo" type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50 md-medium-size-50">
                <md-field>
                  <label>Clave</label>
                  <md-input v-model="clave1" type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50 md-medium-size-50">
                <md-field>
                  <label>Confirmar Clave</label>
                  <md-input v-model="clave2" type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-100 md-medium-size-100">
                <div class="alert alert-success">
                  <span>
                    La clave debe tener una longitud m√≠nima de 8, may√∫sculas,
                    min√≠sculas y un car√°cter especial ! @ # $ % ^ & * _ .
                  </span>
                </div>
              </div>
              <div class="md-layout-item md-size-100 md-medium-size-100 text-right">
                <md-button
                  class="md-raised md-success"
                  @click.native.prevent="Registrar"
                >
                  Procesar</md-button
                >
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>
<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import Crypto from "crypto-js";

export default {
  data() {
    return {
      estatus: false,
      correo: null,
      clave1: null,
      clave2: null,
      nombre: null,
      apellido: null,
      validar: false,
      mens: null
    };
  },
  created() {
    this.Mostrar();
  },
  methods: {
    Mostrar(){
      this.validar = false;
      this.clave1 = null;
      this.clave2 = null;
    },
    Registrar() {
      if (
        this.nombre == null ||
        this.apellido == null ||
        this.correo == null ||
        this.clave1 == null ||
        this.clave2 == null
      ) {
        this.$fire({title:"Debe ingresar los datos solicitados.", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "Debe ingresar los datos solicitados."));
      } else if (
        !/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          this.correo
        )
      ) {
        this.$fire({title:"El correo no es v√°lido", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "El correo no es v√°lido"));
      } else if (
        !/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[_.!@#\$%\^&\*])(?=.{8,})/.test(
          this.clave1
        )
      ) {
        this.$fire({title:"La clave debe tener una longitud m√≠nima de 8, may√∫sculas, min√≠sculas y un car√°cter especial", type:"error", timer:2000});
        //this.$notify(Mensaje(3,"La clave debe tener una longitud m√≠nima de 8, may√∫sculas, min√≠sculas y un car√°cter especial"));
      } else if (this.clave1 != this.clave2) {
        this.$fire({title:"La confirmaci√≥n de clave es incorrecta", type:"success", timer:2000});
        //this.$notify(Mensaje(3, "La confirmaci√≥n de clave es incorrecta"));
      } else {
        this.validar = true;
      }
      if (this.validar) {
        const cifrado = Crypto.HmacSHA1(this.clave1, "btcoin").toString();
        const UPD = new API(this.correo,cifrado);
        UPD.Procesar("registrar",{
          nombre: this.nombre,
          apellido: this.apellido,
        }).then(response => {
            if (response.error) {
              this.$fire({title:response.message, type:"error", timer:2000});
              //this.$notify(Mensaje(3, response.message));
            } else {
              this.$fire({title:response.message, type:"success", timer:2000});
              //this.$notify(Mensaje(0, response.message));
            }  
            this.Mostrar();
        });
      }
    }
  }
};
</script>
