<template>
  <div class="content">
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-33 md-size-33">
        <stats-card data-background-color="green">
          <template slot="header">
            <div style="width:20px;">
              <IconCrypto coinname="BTC" color="color" format="svg" />
            </div>
          </template>
          <template slot="content">
            <p class="category">1 BTC</p>
            <h3 class="title">${{ mercado }}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Cotizaci√≥n de Intercambio
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-33 md-size-33">
        <stats-card data-background-color="green">
          <template slot="header">
            <md-icon>attach_money</md-icon>
          </template>
          <template slot="content">
            <p class="category">Saldo</p>
            <h3 class="title">${{ saldo }}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Valor en BTC {{ (saldo / mercado).toFixed(8) }}
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-33 md-size-33">
        <stats-card data-background-color="blue">
          <template slot="header">
            <div style="width:20px;">
              <IconCrypto coinname="BTC" color="white" format="svg" />
            </div>
          </template>
          <template slot="content">
            <p class="category">Wallet Maestra BTC</p>
            <h3 class="title">{{ balance }}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Saldo de Wallet: ${{ (balance * mercado).toFixed(2) }}
            </div>
          </template>
        </stats-card>
      </div>
    </div>

    <div class="md-layout">
      <div class="md-layout-item md-medium-size-70 md-size-70">
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">Wallet Maestra</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-medium-size-70 md-size-70">
                <md-field>
                  <label>Address</label>
                  <md-input
                    v-model="wallet"
                    type="text"
                    disabled
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-30 md-medium-size-30" v-if="disabled">
                <md-field>
                  <label>Tipo</label>
                  <md-input
                    v-model="tipowallet[p2wpkh]"
                    type="text"
                    disabled
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-30 md-medium-size-30" v-else>
                <md-field>
                  <label>Tipo</label>
                  <md-select v-model="p2wpkh">
                    <md-option
                      v-for="(item, index) in tipowallet"
                      :key="index"
                      :value="index"
                    >
                      {{ item }}
                    </md-option>
                  </md-select>
                </md-field>
              </div>
              <div class="md-layout-item md-size-100 md-medium-size-100">
                <md-field>
                  <label>Private Key (Wallet Import Format)</label>
                  <md-input
                    v-model="WIF"
                    type="password"
                    :disabled="disabled == true"
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-medium-size-40 md-size-40">
                <md-list-item @click="Ver(2)">
                  <div class="stats">
                    <md-icon>visibility</md-icon>Transacciones
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-30 md-medium-size-30" v-if="disabled"></div>
              <div class="md-layout-item md-size-30 md-medium-size-30" v-if="disabled">
                <md-list-item @click="Editar()">
                  <div class="stats"><md-icon>edit</md-icon>Editar</div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-30 md-medium-size-30" v-if="!disabled">
                <md-list-item @click="Guardar()">
                  <div class="stats"><md-icon>save</md-icon>Guardar</div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-30 md-medium-size-30" v-if="!disabled">
                <md-list-item @click="Cerrar()">
                  <div class="stats"><md-icon>close</md-icon>Cerrar</div>
                </md-list-item>
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>

      <div class="md-layout-item md-medium-size-30 md-size-30">
        <stats-card data-background-color="blue">
          <template slot="header">
            <md-icon>qr_code_2</md-icon>
          </template>
          <template slot="content">
            <p class="category">C√≥digo QR</p>
            <img style="width:105px;" :src="qr" />
          </template>
          <template slot="footer">
            <div class="stats"><md-icon>qr_code_2</md-icon>Wallet Maestra</div>
          </template>
        </stats-card>
      </div>
    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="ver == 1">
      <md-card>
        <md-card-header data-background-color="orange">
          <h4 class="title">Utxos</h4>
          <p class="category">Satoshis: {{ utxos_disp }}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="utxos">
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="Hash">{{ item.txid }}</md-table-cell>
              <md-table-cell md-label="Valor">{{
                item.satoshis
              }}</md-table-cell>
              <md-table-cell md-label="Confirmed">{{
                item.confirmed
              }}</md-table-cell>
            </md-table-row>
          </md-table>
        </md-card-content>
      </md-card>
    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="ver == 2">
      <md-card>
        <md-card-header data-background-color="orange">
          <h4 class="title">Operaciones</h4>
          <p class="category">
            Cantidad de operaciones: {{ movimientos.length }}
          </p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="movimientos">
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="TxID">{{ item.hash }}</md-table-cell>
              <md-table-cell md-label="Credito">{{
                item.credito
              }}</md-table-cell>
              <md-table-cell md-label="Debito">{{ item.debito }}</md-table-cell>
              <md-table-cell md-label="Estado">{{
                item.confirmed
              }}</md-table-cell>
            </md-table-row>
          </md-table>
        </md-card-content>
      </md-card>
    </div>
  </div>
</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT();
import QR from "qr-encode";

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      wallet: null,
      qr: null,
      saldo: 0,
      mercado: 0,
      balance: 0,
      WIF: null,
      p2wpkh: 0,
      tipowallet: ['Bech32','P2SH','P2PKH'],
      disabled: true,
      valor: null,
      ver: 0,
      transactions: 0,
      utxos: [],
      utxos_disp: 0,
      movimientos: []
    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      this.wallet = null;
      this.balance = 0;
      this.WIF = null;
      this.disabled = true;
      this.hash = null;
      this.valor = null;
      this.saldo = 0;
      this.transactions = [];
      this.utxos = [];
      this.movimientos = [];
      this.transactions = 0;
      this.ver = 0;
      this.utxos_disp = 0;
      const Consulta = new API();
      Consulta.Ini('configuracion').then(response => {
        this.wallet = response.configuracion.proveedor_address;
        const decWIF = secure.decrypt(
          response.configuracion.proveedor_wif,
          "btcoin"
        );
        this.WIF = decWIF;
        this.p2wpkh = response.configuracion.proveedor_p2wpkh;
        this.saldo = parseFloat(response.saldo).toFixed(2);
        const porcentaje = parseFloat(response.configuracion.price_spread);
        var dataURI = QR(this.wallet, { type: 6, size: 5, level: "Q" });
        var img = new Image();
        img.src = dataURI;
        this.qr = img.src;
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat(result.bpi.USD.rate.replace(",", ""));
          this.mercado = ((btc * (100 + porcentaje)) / 100).toFixed(2);
        });
        Bitcoin.getBalance(this.wallet).then(result => {
          this.transactions =
            result.chain_stats["tx_count"] + result.mempool_stats["tx_count"];
          const received = result.chain_stats["funded_txo_sum"];
          const spent = result.chain_stats["spent_txo_sum"];
          this.balance = ((received - spent) / 100000000).toFixed(8);
        });
        Bitcoin.getUtxo(this.wallet).then(result => {
          this.utxos = result;
          let disp = 0;
          for (let i = 0; i < result.length; i++) {
            if (result[i]["confirmed"]) {
              disp += result[i]["satoshis"];
            }
          }
          this.utxos_disp = disp;
        });
        Bitcoin.getTxs(this.wallet).then(result => {
          result.forEach(element => {
            let salida = 0;
            for (let i = 0; i < element["vin"].length; i++) {
              if (
                element["vin"][i]["prevout"]["scriptpubkey_address"] ==
                this.wallet
              ) {
                salida = salida + element["vin"][i]["prevout"]["value"];
              }
            }
            let entrada = 0;
            for (let i = 0; i < element["vout"].length; i++) {
              if (element["vout"][i]["scriptpubkey_address"] == this.wallet) {
                entrada = entrada + element["vout"][i]["value"];
              }
            }
            let credito = 0;
            let debito = 0;
            if (salida > 0) {
              debito = ((salida - entrada) / 100000000).toFixed(8) + " BTC";
            } else {
              credito = (entrada / 100000000).toFixed(8) + " BTC";
            }

            let value = 0;
            for (let i = 0; i < element["vout"].length; i++) {
              value = value + element["vout"][i]["value"];
            }
            let estado = "Procesando";
            if (element["status"]["confirmed"]) {
              estado = "Procesada";
            }
            this.movimientos.push({
              hash: element["txid"],
              satoshis: (value / 100000000).toFixed(8) + " BTC",
              confirmed: estado,
              credito: credito,
              debito: debito
            });
          });
        });

      });
    },
    Ver(caso) {
      if (this.ver == caso) {
        this.ver = 0;
      } else {
        this.ver = caso;
      }
    },
    Editar() {
      this.disabled = false;
    },
    Guardar() {
      const MiBitcoin = new BT();
      if (!MiBitcoin.isValidWIF(this.WIF)) {
        this.$fire({title:"Address o Private Key No es Inv√°lida.", type:"error", timer:2000});
        //this.$notify(Mensaje(3, "Address o Private Key No es Inv√°lida."));
      } else {
        MiBitcoin.privateKeyToAccount(this.WIF,this.p2wpkh);
        this.wallet = MiBitcoin.defaultAccount.address;
        const securewif = secure.encrypt(this.WIF, "btcoin");
        const UPD = new API();
        UPD.Procesar("updatewalletproveedor",{
          proveedor_address: this.wallet,
          proveedor_wif: securewif,
          proveedor_p2wpkh: this.p2wpkh
        }).then(response => {
          if (response.error) {
            this.$fire({title:response.message, type:"error", timer:2000});
            //this.$notify(Mensaje(3, response.message));
          } else {
            this.$fire({title:response.message, type:"success", timer:2000});
            //this.$notify(Mensaje(0, response.message));
            this.type = "password";
            this.disabled = true;
            this.Mostrar();
            this.TipoWallet();
          }
        });
      }
    },
    Cerrar() {
        this.disabled = true;
    }
  }
};
</script>
