  <div class="content">
    <div class="md-layout">      
      <div class="md-layout-item md-medium-size-25 md-size-25">
        <img src="@/assets/img/stripe.png" width="190"/><br>
        <img src="@/assets/img/visa.png" width="45"/>
        <img src="@/assets/img/mastercard.png" width="30"/>
        <img src="@/assets/img/american.png" width="60"/>
        <img src="@/assets/img/diners.png" width="50"/>
      </div>

      <div class="md-layout-item md-medium-size-75 md-size-75">
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title">
              <md-icon>credit_card</md-icon> Pago a trav√©s de Stripe
            </h4>
            <p class="category">Complete para procesar la transacci√≥n</p>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-75 md-medium-size-75">
                <md-field>
                  <label>Correo electr√≥nico</label>
                  <md-input v-model="correo" type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-25 md-medium-size-25">
                <md-field>
                  <label><strong style="color:green;">Monto $</strong></label>
                  <md-input
                    v-model="amount"
                    type="number"
                    min="10"
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100 md-medium-size-100">
                <stripe-elements v-if="pubKey != null"
                  :pk="pubKey"
                  :amount="amount"
                  ref="elementsRef"
                  @token="tokenCreated"
                  @loading="loading = $event"
                >
                </stripe-elements>
              </div>
              <div class="md-layout-item md-size-100 md-medium-size-100 text-right">
                <md-button
                  class="md-raised md-success"
