<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import * as secure from "securejs";

import { StripeElements } from 'vue-stripe-checkout';

export default {
  components: {
    StripeElements
    
  },
  data() {
    return {
      correo: null,
      amount: 10,
      loading: false,
      pubKey: null,
      publishableKey: process.env.PUBLISHABLE_KEY, 
      token: null,
      charge: null
    };
  },
  created() {
    this.correo = this.$store.state.login;
    const Consulta = new API();
    Consulta.Ini('informacion').then(response => {
      this.pubKey = secure.decrypt(response.configuracion.pubKey_stripe,"btcoin");
    });
  },
  methods: {
    submit () {
      this.$refs.elementsRef.submit();
    },
    tokenCreated (token) {
      this.token = token;
      this.charge = {
        source: token.card,
        amount: this.amount, // the amount you want to charge the customer in cents. $100 is 1000 (it is strongly recommended you use a product id and quantity and get calculate this on the backend to avoid people manipulating the cost)
        description: this.correo // optional description that will show up on stripe when looking at payments
      }
      this.$fire({
        title: "¬øEst√° seguro de que desea continuar?",
        text: "Pago por Stripe de $"+this.amount,
        type: "question",
        confirmButtonText: "Si",
        showCancelButton: true,
        cancelButtonText: "No",
      }).then(() => {
        this.sendTokenToServer(this.charge);
        this.$confirm("Token ID: "+token.id,"Procesado $"+this.amount,"success").then(() => {
        });
      });
    },
    sendTokenToServer (charge) {
      // Send to charge to your backend server to be processed
      // Documentation here: https://stripe.com/docs/api/charges/create  
    }
  }
};
</script>
