<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT('testnet');

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },
  data() {
    return {
      caso: 0,
      reporte: [],
      saldo: 0,
      balance: 0,
      monto1: 0,
      monto2: 0,
      monto3: 0,
      portafolio: [],
      invertir: 0,
      solicitud: 0,
      detalle_inversiones: [],
      detalle_solicitudes: [],
      estatus: ['Negada','En Proceso','Aprobada'],
      disabled: true,
    };
  },
  created () {
    this.Mostrar();
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.portafolio = [];
      this.caso =  0;
      this.monto1 =  0;
      this.monto2 =  0;
      this.monto3 =  0;
      this.invertir =  0;
      this.detalle_inversiones = [];
      this.detalle_solicitudes = [];
      this.solicitud =  0;
      this.balance =  0;
      this.disabled =  true;
      this.saldo = 0;    
      this.transactions = null;
      const Consulta = new API();
      Consulta.Ini('informacion').then(response => {
        this.reporte = response;
        const user = response.user;
        this.saldo = parseFloat(response.saldo).toFixed(2);
        Bitcoin.getBalance(user.wallet).then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Consulta.Ini('marketplace').then(response => {
          response.portafolio.forEach(element => {
            this.portafolio.push({
              id: element.id,
              title: element.titulo,
              multiplo: element.multiplo,
            });
          });
        });
        if (response.completo_inversiones) {
          response.completo_inversiones.forEach(element => {
            this.detalle_inversiones.push({
              id: element.id,
              fecha: element.fecha,
              titulo: element.titulo,
              monto: element.monto
            });
          });
        } else {
          response.detalle_inversiones.forEach(element => {
            this.detalle_inversiones.push({
              id: element.id,
              fecha: element.fecha,
              titulo: element.titulo,
              monto: element.monto
            });
          });
        }
        if (response.solicitudes) { this.detalle_solicitudes = response.solicitudes; }
      });
    },
    Caso(index) {
      if (this.caso != index) {
        this.caso = index;
      } else {
        this.caso = 0;
      }
    },
    Maximo(caso) {
      if (caso == 1){
        this.monto1 = this.detalle_inversiones[this.solicitud]['monto'];
      }
      if (caso == 2){
        this.monto2 = this.reporte.rentabilidad;        
      }
      if (caso == 3){
        this.monto3 = this.reporte.rentabilidad;               
      }
    },
    Procesar(caso) {
      const UPD = new API();
      if (caso == 1 && this.monto1>0){
        this.$confirm("Confirme la Operaci√≥n").then(() => {
        UPD.Procesar("gestion_capital",{
          monto: this.monto1,
          inversion: this.detalle_inversiones[this.solicitud]
        }).then(response => {
          if (response.error){
            this.$fire({title:response.message, type:"error", timer:2000});
            this.$notify(Mensaje(3,response.message));
          } else {
            this.$fire({title:response.message, type:"success", timer:2000});
            //this.$notify(Mensaje(0,response.message));
            this.Mostrar();
          }
        });
        });
      } else if (caso == 2 && this.monto2>0){
        this.$confirm("Confirme la Operaci√≥n").then(() => {
        UPD.Procesar("gestion_rentabilidad",{
          monto: this.monto2,
        }).then(response => {
          if (response.error){
            this.$fire({title:response.message, type:"error", timer:2000});
            this.$notify(Mensaje(3,response.message));
          } else {
            this.$fire({title:response.message, type:"success", timer:2000});
            //this.$notify(Mensaje(0,response.message));
            this.Mostrar();
          }
        });
        });
      } else if (caso == 3 && this.monto3>0){
        this.$confirm("Confirme la Operaci√≥n").then(() => {
        UPD.Procesar("gestion_reinvertir",{
          monto: this.monto3,
          portafolio: this.portafolio[this.invertir]
        }).then(response => {
          if (response.error){
            this.$fire({title:response.message, type:"error", timer:2000});
            this.$notify(Mensaje(3,response.message));
          } else {
            this.$fire({title:response.message, type:"success", timer:2000});
            //this.$notify(Mensaje(0,response.message));
            this.Mostrar();
          }
        });
        });
      } else {
        this.$fire({title:"Debes colocar el monto", type:"error", timer:2000});
      }
    }

  }
};
</script>
