<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT();
const MiBitcoin = new BT();


import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      wallet: null,
      saldo: 0,
      mercado: 0,
      balance: 0,
      WIF: null,
      disabled: true,
      valor: null,
      vertrans: false,
      verutxo: false,
      transactions: 0,
      fsalida: 0,
      utxos: [],
      utxos_disp: 0,
      miutxos: [],
      miutxos_disp: 0,
      movimientos: [],
      direcciones: [],
      caso: 0,
      recibe: null,
      recibe_btc: null,
      montodolar: 0,
      montobtcenv: 0,
      dolar: 0,
      montobtc: 0,
      decWIF: null,
      tarifa: 0,
      fees: {
        nombre: ["Baja","Media","Alta"],
        tipo:   ["hourFee","halfHourFee","fastestFee"],
        valor:  [0,0,0,0]
      },
      feesEnv: {
        nombre: ["Baja","Media","Alta"],
        tipo:   ["hourFee","halfHourFee","fastestFee"],
        valor:  [0,0,0]
      },
      comision: 0,
      comis: 0,
      comisiontotal: 0,
      porcentaje: 0,
      opcionenvio: ["Dolar $","Bitcoin","Red Bitcoin"],
      formaenvio: 0,
      verifenviobtc: null,
      comision_address: null,
      comision_salida: null
    };
  },
  created () {
    this.Mostrar();
    
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.wallet =  null;
      this.balance =  0;
      this.WIF =  null;
      this.Hex =  null;
      this.disabled =  true;
      this.hash = null;
      this.direcciones = [];
      this.valor = null;
      this.saldo = 0;
      this.montodolar = 0;
      this.montobtcenv = 0;
      this.montobtc = 0;
      this.recibe = null;
      this.recibe_btc = null;
      this.movimientos= [];
      this.transactions = null;
      this.decWIF = null;
      this.comision = 0;
      this.fsalida = 0;
      this.comis = 0;
      this.comisiontotal = 0;
      this.porcentaje = 0;
      this.disponibilidad = true;
      this.verifenviobtc = null;
      this.comision_address=null;
      this.comision_salida=null;
      const Consulta = new API();
      Consulta.Ini('informacion').then(response => {
        const user = response.user;
        this.wallet = user.wallet;
        this.WIF = secure.decrypt(user.wif, 'btcoin');
        this.Hex = secure.decrypt(user.hex, 'btcoin');
        MiBitcoin.privateKeyToAccount(this.WIF,response.user.p2wpkh);
        this.saldo = parseFloat(response.saldo).toFixed(2);
        this.fsalida = parseFloat(response.configuracion.retiro_flat);
        this.porcentaje = parseFloat(response.configuracion.price_spread);
        this.comision = parseFloat(response.configuracion.comision);
        this.comision_address=response.configuracion.comision_address;
        this.comision_salida=response.configuracion.salida_address;
        this.decWIF = response.configuracion.proveedor_wif;
        this.movimientos =  response.reporte;

        Bitcoin.getBalance(this.wallet).then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+this.porcentaje))/100).toFixed(2);
        });
        Bitcoin.privateKeyToAccount(secure.decrypt(response.configuracion.proveedor_wif, 'btcoin'),response.configuracion.proveedor_p2wpkh);
        Bitcoin.getUtxo().then(result => {
          this.utxos = result;
        });
        MiBitcoin.getUtxo().then(result => {
          this.miutxos = result;
        });
        Bitcoin.estimateFee().then(result => {
          this.fees.valor[0] = result.hourFee;
          this.fees.valor[1] = result.halfHourFee;
          this.fees.valor[2] = result.fastestFee;
          this.fees.valor[3] = result.fastestFee/4;
          this.feesEnv.valor[0] = result.hourFee;
          this.feesEnv.valor[1] = result.halfHourFee;
          this.feesEnv.valor[2] = result.fastestFee;
        });

        Consulta.Ini('direcciones').then(response => {
            this.direcciones = response.direcciones;
        });

      });
    },
    Maximo(caso) {
      if (caso == 1){
        this.montodolar =(this.saldo/(1+(this.comision/100))).toFixed(2);
      }
      if (caso == 2){
        const feePerByte = this.fees.valor[this.tarifa];
        const change = 0;
        const fee = this.getTxSize(this.miutxos.length, change > 0 ? 2 : 1) * feePerByte;
        let feesal = 0;
        if (this.formaenvio>1){feesal = (this.fsalida*this.mercado/100000000);} 
        else {feesal = 0;}
        const total = ((this.balance*this.mercado - feesal - (fee*this.mercado/100000000))/(1+(this.comision/100))-0.01).toFixed(2);
        this.montobtcenv = total;  
      }
      if (caso == 3){
        this.dolar = this.saldo;  
      }
    },
    Enviar() {
      if (this.recibe==null && this.formaenvio==0) {
        this.$fire({title:"Debe ingresar los datos solicitados", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));
      } else if (this.recibe_btc==null && this.formaenvio>1) {
        this.$fire({title:"Debe ingresar los datos solicitados.", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));
      } else if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/).test(this.recibe) && this.formaenvio<2){
        this.$fire({title:"El correo no es v√°lido.", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'El correo no es v√°lido.'));
      } else if (this.direcciones[this.recibe]==null && this.formaenvio<2) {
        this.$fire({title:"El correo no registrado en la plataforma", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'El correo no registrado en la plataforma'));
      } else if (!MiBitcoin.isValidAddress(this.direcciones[this.recibe]) && this.formaenvio==1) {
        this.$fire({title:"Problema con la Wallet asociada al Correo", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'Problema con la Wallet asociada al Correo'));
      } else if (!MiBitcoin.isValidAddress(this.recibe_btc) && this.formaenvio==2) {
        this.$fire({title:"La Wallet no es v√°lida", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'La Wallet no es v√°lida'));
      } else if (this.montodolar==0  && this.formaenvio==0) {
        this.$fire({title:"Debe ingresar un monto a enviar.", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'Debe ingresar un monto a enviar.'));
      } else if (this.montobtcenv==0  && this.formaenvio>0) {
        this.$fire({title:"Debe ingresar un monto a enviar.", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'Debe ingresar un monto a enviar.'));
      } else {
        this.$confirm("Confirme la Operaci√≥n").then(() => {
          if(this.formaenvio>0) {
            this.CalculoMercado();
            const tipofee = this.feesEnv.tipo[this.tarifa];
            const resultfee = this.VerComisionEnv(this.formaenvio,'fee');
            const comistotal = this.VerComisionEnv(this.formaenvio,'total');
            const comist = this.VerComisionEnv(this.formaenvio,'comision');
            const satoshi = parseFloat(resultfee) + parseFloat((100000000*(this.montobtcenv/this.mercado)).toFixed(0));
            const fee_uso = parseFloat((100000000*(comist/this.mercado)).toFixed(0));
            const monto_total = parseFloat(this.montobtcenv)+parseFloat(comistotal);
            const feesalida = parseFloat(this.fsalida).toFixed(0);
            const to_value = [];
            if (this.formaenvio>1){
              to_value.push({to: this.recibe_btc, value: satoshi});
              to_value.push({to: this.comision_address, value: fee_uso});
              to_value.push({to: this.comision_salida, value: this.fsalida});          
            } else {
              to_value.push({to: this.direcciones[this.recibe], value: satoshi});          
              to_value.push({to: this.comision_address, value: fee_uso});
            }
            MiBitcoin.sendTransaction(to_value,tipofee).then(result => {
                const UPD = new API();
                UPD.Procesar("enviarbtc",{
                  satoshi: satoshi,
                  txid: result.txid,
                  tipo: this.formaenvio,
                }).then(response => {
                  if (response.error){
                    this.$fire({title:response.message, type:"error", timer:2000});
                    //this.$notify(Mensaje(3,response.message));
                  } else {
                    this.$fire({title:response.message, type:"success", timer:2000});
                    //this.$notify(Mensaje(0,response.message));
                  }
                });
                this.$fire({
                  title: "Se proces√≥ el Env√≠o",
                  text: "TXID: "+result.txid,
                  type: "success",
                }).then(r => {        
                  this.saldo = 0;
                  this.Mostrar();
                });              
            }).catch(err => {
              this.$fire({title:"Disculpe, Intente mas tarde.", type:"success", timer:2000});
              //this.$notify(Mensaje(3,'Disculpe, Intente mas tarde.'));
              //this.$notify(Mensaje(3,err));
            });
          } else {
            const UPD = new API();
            UPD.Procesar("enviardinero",{
              send: this.recibe,
              monto: this.montodolar
            }).then(response => {
              if (response.error){
                this.$fire({title:response.message, type:"error", timer:2000});
                //this.$notify(Mensaje(3,response.message));
                this.saldo = 0;
                this.Mostrar();
              } else {
                this.$fire({title:response.message, type:"success", timer:2000});
                //this.$notify(Mensaje(0,response.message));
                this.$fire({
                  title: "Se proces√≥ el Env√≠o",
                  text: "Desino: "+this.recibe,
                  type: "success",
                }).then(r => {        
                  this.saldo = 0;
                  this.Mostrar();
                });
              }
            });
          }
        });
      }

    },
    Cambiar() {
      if (this.dolar==0 || (parseFloat(this.dolar)>parseFloat(this.saldo))) {
        this.$fire({title:"Debe ingresar un monto a Intercambiar.", type:"success", timer:2000});
        //this.$notify(Mensaje(3,'Debe ingresar un monto a Intercambiar.'));
      } else {
      this.$confirm("Confirme la Operaci√≥n").then(() => {
        this.CalculoMercado();
        const tipofee = this.fees.tipo[this.tarifa];
        const resultfee = this.VerComision('fee');
        const comistotal = this.VerComision('total');
        const comist = this.VerComision('comision');
        const satoshi = parseFloat(resultfee) + parseFloat((100000000*(this.dolar/this.mercado)).toFixed(0));
        const fee_uso = parseFloat((100000000*(comist/this.mercado)).toFixed(0));
        const monto_total = parseFloat(this.dolar)+parseFloat(comistotal);
        const price_spread = parseFloat(this.mercado*100/(100+this.porcentaje)).toFixed(2);
        const to_value = [];
        to_value.push({to: this.wallet, value: satoshi});
        to_value.push({to: this.comision_address, value: fee_uso});
        if (this.tarifa<3) {
          Bitcoin.sendTransaction(to_value,tipofee).then(result => {
            const UPD = new API();
            UPD.Procesar("intercambiar",{
              fees:     resultfee,
              satoshi:  satoshi,
              txhash:   result.txid,
              mercado:  parseFloat(this.mercado).toFixed(2),
              monto_total:    monto_total,
              origen:     Bitcoin.defaultAccount.address,
              destino:     this.wallet,
              monto_cambiar:    parseFloat(this.dolar),
              fee_uso: comist,
              price_spread: price_spread,
              fee_uso_satoshi: fee_uso
            }).then(response => {
              if (response.error){
                this.$fire({title:response.message, type:"error", timer:2000});
                //this.$notify(Mensaje(3,response.message));
              } else {
                //this.$notify(Mensaje(0,response.message));
                this.$fire({
                  title: "Se proces√≥ el Intercambio ",
                  text: "TXID: "+result.txid,
                  type: "success",
                }).then(r => {        
                  this.saldo = 0;
                  this.Mostrar();
                });
              }
            });
          }).catch(err => {
            this.$fire({title:"Disculpe, Intente mas tarde.", type:"success", timer:2000});
            //this.$notify(Mensaje(3,'Disculpe, Intente mas tarde.'));
            //this.$notify(Mensaje(3,err));
          });
        } else {
            const UPD = new API();
            UPD.Procesar("intercambiar_lote",{
              fees:     resultfee,
              satoshi:  satoshi,
              txhash:   '',
              mercado:  this.mercado,
              monto_total: monto_total,
              origen:     Bitcoin.defaultAccount.address,
              destino:     this.wallet,
              monto_cambiar:    parseFloat(this.dolar),
              fee_uso: comist,
              price_spread: price_spread,
              fee_uso_satoshi: fee_uso
            }).then(response => {
              if (response.error){
                this.$fire({title:response.message, type:"error", timer:2000});
                this.$notify(Mensaje(3,response.message));
              } else {
                this.$fire({title:response.message, type:"success", timer:2000});
                this.$notify(Mensaje(0,response.message));
              }
            });
        }
      });

      }
    },
    Caso(index) {
      if (this.caso == index) {
        this.caso = 0;
      } else {
        this.caso = index;
      }
    },
    getTxSize(vin, vout) {
      return vin * 180 + vout * 34 + 10 + vin;
    },
    VerComision(caso) {
      this.CalculoMercado();
      const value = (100000000*this.dolar/this.mercado).toFixed(0);
      const feePerByte = this.fees.valor[this.tarifa];
      let ninputs = 0;
      let availableSat = 0;
      for (let i = 0; i < this.utxos.length; i++) {
        if (this.utxos[i]['confirmed']) {
          availableSat += this.utxos[i].['satoshis'];
          ninputs++;
          if (availableSat >= value) break
        }
      }
      if (availableSat < value) {
          this.disponibilidad=false;
      } else {
        this.disponibilidad=true;
        const change = availableSat - value;
        const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte;
        const total = ((fee*this.mercado/100000000)+(this.dolar*this.comision/100)).toFixed(2);
        const comis = ((this.dolar*this.comision/100)).toFixed(2);
        if (this.dolar>0 && caso == 'total'){
          return total;
        } else if (this.dolar>0 && caso == 'comision'){
          return comis;
        } else if (this.dolar>0 && caso == 'fee'){
          return fee;
        } else {
          return 0;
        }
      }
    },
    VerComisionEnv(forma,caso) {

    if (forma > 0) {
        this.CalculoMercado();
        const value = (100000000*this.montobtcenv*(1+this.comision/100)/this.mercado).toFixed(0);
        const feePerByte = this.feesEnv.valor[this.tarifa];
        let ninputs = 0;
        let availableSat = 0;
        for (let i = 0; i < this.miutxos.length; i++) {
          if (this.miutxos[i]['confirmed']) {
            availableSat += this.miutxos[i].['satoshis'];
            ninputs++;
            if (availableSat >= value) break
          }
        }
        if (availableSat < value) {
            this.disponibilidad=false;
        } else {
          
          let feesal = 0;
          if (forma>1){feesal = (this.fsalida*this.mercado/100000000);
          } else {
            feesal = 0;
          }
          this.disponibilidad=true;
          const change = availableSat - value;
          const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte;
          const total = ((feesal)+(fee*this.mercado/100000000)+(this.montobtcenv*this.comision/100)).toFixed(2);
          const comis = ((this.montobtcenv*this.comision/100)).toFixed(2);
          if (this.montobtcenv>0 && caso == 'total'){
            return total;
          } else if (this.montobtcenv>0 && caso == 'comision'){
            return comis;
          } else if (this.montobtcenv>0 && caso == 'fee'){
            return fee;
          } else {
            return 0;
          }
          
        }
      } else {
        const total = ((this.montodolar*this.comision/100)).toFixed(2);
        return total;
      }
    },
    CalculoMercado() {
      Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.data.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+this.porcentaje))/100).toFixed(2);
      });
    }

  }
};
</script>
