<template>
  <div class="content">
    <div class="md-layout">

      <div
        class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
        style="background-color:#6bac07;"
      >
            <h5 class="title" ><strong style="color:white;">Invierte en Trading</strong></h5>
      </div>

      <div
        class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
        v-for="(stats, index) in statsCards"
        :key="index"
      >
        <stats-card :data-background-color="stats.type">
          <template slot="header">
            <md-icon>{{ stats.icon }}</md-icon>
            <p class="category">{{ stats.etap }}</p>
            <p class="category">Inversionistas: {{ stats.invers }}</p>
          </template>
          <template slot="content">
            <p>Monto Objetivo de la Recaudaci√≥n: ${{ stats.value }}</p>
            <p><md-icon>date_range</md-icon>Fecha tope {{ stats.footerFin }}</p>
            <h2 class="title">
              <string>{{ stats.title }}</string>
            </h2>

            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-50 md-medium-size-50">
                <h3 class="title">
                  Monto Recaudado: <font color="red">${{ stats.monto }}</font>
                </h3>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-30 md-medium-size-30">
                <md-field>
                  <label><strong style="color:green;">Monto $</strong></label>
                  <md-input
                    v-model="stats.invertir"
                    type="number"
                    :min="stats.multiplo"
                    :max="saldo"
                    :step="stats.multiplo"
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-20 md-medium-size-10  md-xsmall-size-100">
                <md-button class="md-round md-success" @click="Invertir(index)">
                  Invertir</md-button
                >
              </div>

              <div class="md-layout-item md-small-size-100 md-size-60">
                <p>{{ stats.descr }}</p>
              </div>
            </div>
          </template>
        </stats-card>
      </div>
    </div>
  </div>
</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },
  data() {
    return {
      correo: "",
      clave: "",
      correo_reg: "",
      clave_reg: "",
      clave_reg2: "",
      saldo: 0,
      autenticado: false,
      estatus: false,
      statsCards: [],
      icono: ["trending_up", "done_all", "lock"],
      etapa: ["En Proceso", "Completo", "Cerrado"],
      tipo: ["blue", "green", "red"],
      reporte: []
    };
  },
  created() {
    this.Mostrar();
  },
  methods: {
    Mostrar() {
      const Consulta = new API();
      this.reporte = [];
      Consulta.Ini('marketplace').then(response => {
        this.saldo = response.saldo;
        response.portafolio.forEach(element => {
          this.statsCards.push({
            id: element.id,
            etapa: element.etapa,
            icon: this.icono[element.etapa],
            type: this.tipo[element.etapa],
            etap: this.etapa[element.etapa],
            title: element.titulo,
            descr: element.descripcion,
            value: element.meta,
            monto: element.monto,
            invertir: element.multiplo,
            multiplo: element.multiplo,
            satoshi: 0,
            footerIni: "Fecha de Inicio: " + element.fecha_ini,
            footerFin: element.fecha_fin,
            invers: element.invers,
            porcentaje: Math.round((element.monto / element.meta) * 100),
            footerIcon: "ti-time"
          });
        });
      });
    },
    Invertir(index) {
      if (!this.statsCards[index].invertir) {
        this.$fire({title:"Debe colocar el monto a invertir", type:"success", timer:2000});
        //this.$notify(Mensaje(3, "Debe colocar el monto a invertir"));    
      } else {
        this.$confirm("Confirme la Operaci√≥n").then(() => {
          const UPD = new API();
          UPD.Procesar("invertir",{
            portafolio: this.statsCards[index].id,
            monto: this.statsCards[index].invertir,
          }).then(response => {
            if (response.error) {
              this.$fire({title:response.message, type:"error", timer:2000});
              //this.$notify(Mensaje(3, response.message));
            } else {
              this.$fire({title:response.message, type:"success", timer:2000});
              //this.$notify(Mensaje(2, response.message));
              this.statsCards = [];
              this.Mostrar();
            }
          });
        });
      }
    }
  }
};
</script>