    <div class="md-layout" v-if="procesar">
      <div class="md-layout-item md-medium-size-70 md-size-10">
        <md-card>
          <md-card-header data-background-color="orange">
            <h4 class="title">Productos Seleccionados</h4>
            <p class="category">Cantidad: {{ carrito.length }}</p>
          </md-card-header>
          <md-card-content>
            <md-table v-model="carrito">
              <md-table-row slot="md-table-row" slot-scope="{ item, index }">
                <md-table-cell md-label="Productos">
                  <img
                    style="width:100px;"
