                  <md-list-item @click="Bajar(item.categoria, index)">
                    <i class="material-icons">remove</i>
                  </md-list-item>
                </md-table-cell>
                <md-table-cell md-label="Precio $">
                  {{ (item.cantidad * item.precio).toFixed(2) }}
