                <md-table-cell md-label="Precio BTC">
                  {{ ((item.cantidad * item.precio) / mercado).toFixed(8) }}
                </md-table-cell>
              </md-table-row>
            </md-table>
          </md-card-content>
        </md-card>
      </div>

      <div class="md-layout-item md-medium-size-30 md-size-100">
        <md-card>
          <md-card-header data-background-color="purple">
            <h4 class="title">Balance</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-30">
                <i class="material-icons">attach_money</i>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-70">
                <h3 class="title">${{ saldo.toFixed(2) }}</h3>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-30">
                BTC
              </div>
              <div class="md-layout-item md-small-size-100 md-size-70">
                <h3 class="title">${{ (balance * mercado).toFixed(2) }}</h3>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-30">
                <font color="red">Deuda:</font>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-70">
                <h3 class="title">
                  <font color="red">-${{ monto_total.toFixed(2) }}</font>
                </h3>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-30">
                Resta:
              </div>
              <div
                class="md-layout-item md-small-size-100 md-size-70"
                v-if="saldo + balance * mercado - monto_total > 0"
              >
                <h3 class="title">
                  <font color="blue"
                    >${{
                      (saldo + balance * mercado - monto_total).toFixed(2)
                    }}</font
                  >
                </h3>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-70" v-else>
                <h3 class="title">
                  <font color="red"
                    >${{
                      (saldo + balance * mercado - monto_total).toFixed(2)
                    }}</font
                  >
                </h3>
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>

      <div class="md-layout-item md-medium-size-70 md-size-100">
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">Procesar Pago</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-20">
                <md-field>
                  <label>Forma de Pago</label>
                  <md-select v-model="formapago">
                    <md-option
                      v-for="(item, index) in opcionpago"
                      :key="index"
                      :value="index"
                    >
                      {{ item }}
                    </md-option>
                  </md-select>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-60">
                <md-field>
                  <label>Wallet de Recepci√≥n</label>
                  <md-input
                    v-model="wallet_destino"
                    type="text"
                    disabled
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-20">
                <md-field>
                  <label>Comisi√≥n $</label>
                  <md-input
                    v-bind:value="VerComision('total')"
                    disabled
                    type="number"
                    step="0.01"
                  ></md-input>
                </md-field>
              </div>
              <div
                class="md-layout-item md-small-size-100 md-size-20"
                v-if="formapago != 0"
              >
                <md-field>
                  <label>Tarifa</label>
                  <md-select v-model="tarifa">
                    <md-option
                      v-for="(item, index) in fees.nombre"
                      :key="index"
                      :value="index"
                    >
                      {{ item }}
                    </md-option>
                  </md-select>
                </md-field>
              </div>
              <div
                class="md-layout-item md-small-size-100 md-size-20"
                v-if="formapago == 2"
              >
                <md-field>
                  <label>Monto BTC</label>
                  <md-input
                    v-model="pago_dolar"
                    type="number"
                    step="0.0000001"
                    min="0"
                    v-bind:value="PagoMixto()"
                    disabled
                  ></md-input>
                </md-field>
              </div>
              <div
                class="md-layout-item md-small-size-100 md-size-20"
                v-if="formapago == 2"
              >
                <md-field>
                  <label>Monto $</label>
                  <md-input
                    v-model="pago_dolar"
                    type="number"
                    step="0.01"
                    min="0"
                    :max="saldo"
                  ></md-input>
                </md-field>
              </div>
              <div
                class="md-layout-item md-size-100 text-right"
                v-if="formapago == 0"
              >
                <md-button class="md-round " @click="Pagar()">
                  Procesar</md-button
                >
              </div>
              <div
                class="md-layout-item md-size-80 text-right"
                v-if="formapago == 1"
              >
                <md-button class="md-round " @click="Pagar()">
                  Procesar</md-button
                >
              </div>
              <div
                class="md-layout-item md-size-40 text-right"
                v-if="formapago == 2"
              >
                <md-button class="md-round " @click="Pagar()">
                  Procesar</md-button
                >
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>

      <div class="md-layout-item md-medium-size-30 md-size-100">
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title">QR Pago Bitcoin</h4>
          </md-card-header>
          <md-card-content>
            <img style="width:250px;" :src="qr" />
          </md-card-content>
