    <div class="md-layout" v-else>
      <div class="md-layout-item md-small-size-100 md-size-25">
        <md-field>
          <label>Categor√≠as</label>
          <md-select v-model="categoria">
            <md-option
              v-for="(item, index) in categorias"
              :key="index"
              :value="index"
            >
              {{ item }}
            </md-option>
          </md-select>
        </md-field>
      </div>
    </div>

    <div class="md-layout" v-if="!procesar">
      <div
        class="md-layout-item md-medium-size-50 md-xsmall-size-100 md-size-25"
        v-for="(stats, index) in catalago[categoria]"
        :key="index"
      >
        <stats-card :data-background-color="stats.color">
          <template slot="header">
            <h3 style="width:150px;" class="title">
              $ {{ parseFloat(stats.precio).toFixed(2) }}
            </h3>
          </template>
          <template slot="content">
            <p class="category">BTC</p>
            {{ stats.width }}
            <h3 class="title">
              {{ (parseFloat(stats.precio) / mercado).toFixed(8) }}
            </h3>
            <img :style="`width:${stats.width}px;`" :src="`${stats.imagen}`" />
            <p class="category">{{ stats.descripcion }}</p>
          </template>
          <template slot="footer">
            <div class="stats">
              <md-icon>access_time</md-icon>Disponibles: {{ stats.disponible }}
            </div>
            <div
              class="md-layout-item md-size-30 text-right"
              v-if="stats.activo"
            >
              <md-button class="md-round " @click="Agregar(categoria, index)">
                Agregar</md-button
              >
            </div>
            <div class="md-layout-item md-size-30 text-right" v-else>
              <i class="material-icons">done_all</i>
            </div>
          </template>
        </stats-card>
      </div>
    </div>
  </div>
</template>
<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT();
import QR from "qr-encode";

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },
  data() {
    return {
      categoria: 0,
      categorias: ['Productos','Servicios','Vaciones Prepagadas'],
      carrito: [],
      catalago: [],
      monto_total: 0,
      procesar: false,
      opcionpago: ["Dolar $","Bitcoin","Mixto"],
      formapago: 0,
      pago_dolar: 0,
      pago_btc: 0,
      boton: true,
      qr: null,

      wallet: null,
      wallet_destino: null,
      saldo: 0,
      mercado: 0,
      balance: 0,
      WIF: null,
      Hex: null,
      disabled: true,
      hash: null,
      valor: null,
      transactions: null,
      caso: 0,
      recibe: null,
      montodolar: 0,
      dolar: 0,
      montobtc: 0,
      decWIF: null,
      tarifa: 0,
      fees: {
        nombre: ["Baja","Media","Alta"],
        tipo:   ["hourFee","halfHourFee","fastestFee"],
        valor:  [0,0,0]
      },
      comision: 0,
      comis: 0,
      comisiontotal: 0,
      porcentaje: 0,
      utxos: null,
      table1: {
        title: "Reporte de Transacciones",
        subTitle: "",
        columns: ["Fecha","Descripci√≥n","Monto"],
        data: [],
        edicion: true
      },

    };
  },
  created () {
    this.Mostrar();


  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    },
  },
  methods: {
    Mostrar() {
      this.wallet =  null;
      this.balance =  0;
      this.WIF =  null;
      this.Hex =  null;
      this.wallet =  null;
      this.wallet_destino =  null;
      this.disabled =  true;
      this.hash = null;
      this.valor = null;
      this.saldo = 0;
      this.transactions = null;
      this.decWIF = null;
      this.qr = null;
      this.comision = 0;
      this.comis = 0;
      this.comisiontotal = 0;
      this.porcentaje = 0;
      this.disponibilidad = true;
      this.catalago = [];
      API({
        action:  'informacion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        this.wallet_destino = response.data.configuracion.servicio_address;
        var dataURI = QR(this.wallet_destino, {type: 6, size: 5, level: 'Q'});
        var img = new Image();
        img.src = dataURI;
        this.qr = img.src;
        this.catalago = response.data.catalago;
        const user = response.data.user;
        this.wallet = user.wallet;
        this.WIF = secure.decrypt(user.wif, 'btcoin');
        this.Hex = secure.decrypt(user.hex, 'btcoin');
        this.saldo = parseFloat(response.data.saldo);
        this.porcentaje = parseFloat(response.data.configuracion.price_spread);
        this.comision = parseFloat(response.data.configuracion.comision);
        this.decWIF = response.data.configuracion.proveedor_wif;
        Bitcoin.getBalance(this.wallet).then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+this.porcentaje))/100).toFixed(4);
        });
        Bitcoin.privateKeyToAccount(secure.decrypt(response.data.configuracion.proveedor_wif, 'btcoin'));
        Bitcoin.getUtxo().then(result => {
          this.utxos = result;
        });
        if (response.data.reporte){
            response.data.reporte.forEach(element => {
            this.table1.data.push({
              fecha: element['fecha'],
              descripci√≥n: element['descripcion'],
              monto: "$ "+(element['monto']*element['tipo']).toFixed(4),
            });
          });
        }
        Bitcoin.estimateFee().then(result => {
          this.fees.valor[0] = result.hourFee;
          this.fees.valor[1] = result.halfHourFee;
          this.fees.valor[2] = result.fastestFee;
        });
      });

    },
    MontoPagar() {
      this.boton = true;
      let inputs = 0;
      for (let i = 0; i < this.carrito.length; i++) {
        inputs += this.carrito[i].precio*this.carrito[i].cantidad;
      }
      this.monto_total = inputs;
      if (this.formapago==0 && this.monto_total>this.saldo){
        this.boton = false;
      }
    },
    Agregar(categoria,index) {
      this.carrito.push({
        indice: index,
        categoria: categoria,
        id: this.catalago[categoria][index].id,
        precio: this.catalago[categoria][index].precio,
        cantidad: 1
      });
      this.monto_total = this.monto_total + parseFloat(this.catalago[categoria][index].precio);
      this.catalago[categoria][index].activo = false;
      this.catalago[categoria][index].color = "gray";
      this.MontoPagar();
    },
    VaciarTdos() {
      for (let i = 0; i < this.carrito.length; i++) {
        this.catalago[this.carrito[i].categoria][this.carrito[i].indice].activo = true;
        this.catalago[this.carrito[i].categoria][this.carrito[i].indice].color = "green";
      }
      this.carrito = [];
      this.MontoPagar();
      if (this.carrito.length == 0 && this.procesar) {
        this.procesar = !this.procesar;
      }
    },
    Vaciar(categoria,index) {
      this.catalago[this.carrito[index].categoria][this.carrito[index].indice].activo = true;
      this.catalago[this.carrito[index].categoria][this.carrito[index].indice].color = "green";
      this.carrito.splice(index, 1);
      this.MontoPagar();
      if (this.carrito.length == 0 && this.procesar) {
        this.procesar = !this.procesar;
      }

    },
    Procesar() {
      if (this.carrito.length>0 || this.procesar){
        this.procesar = !this.procesar;
      }
    },
    Subir(categoria,index) {
      if (this.carrito[index].cantidad<this.catalago[this.carrito[index].categoria][this.carrito[index].indice].disponible){
        this.carrito[index].cantidad ++;
        this.MontoPagar();
      }
    },
    Bajar(categoria,index) {
      if (this.carrito[index].cantidad>1){
        this.carrito[index].cantidad --;
        this.MontoPagar();
      }
    },
    PagoMixto() {
      this.pago_btc = ((this.monto_total-this.pago_dolar)/this.mercado).toFixed(8);
      return this.pago_btc;
    },














    Enviar() {
      if (this.recibe==null) {
        this.$fire({title:"Debe ingresar los datos solicitados.", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));
      } else if (this.montodolar==0) {
        this.$fire({title:"Debe ingresar un monto a enviar.", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'Debe ingresar un monto a enviar.'));
      } else if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/).test(this.recibe)){
        this.$fire({title:"El correo no es v√°lido.", type:"success", timer:2000});
        //this.$notify(Mensaje(3,'El correo no es v√°lido.'));
      } else {
      API({
        action:  'enviardinero',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        send: this.recibe,
        monto: this.montodolar
      }).then(response => {
        if (response.data.error){
          this.$fire({title:response.message, type:"error", timer:2000});
          //this.$notify(Mensaje(3,response.data.message));
          this.saldo = 0;
          this.Mostrar();
        } else {
          this.$fire({title:response.message, type:"success", timer:2000});
          //this.$notify(Mensaje(2,response.data.message));
          this.saldo = 0;
          this.Mostrar();
        }
      });
      }
    },
    Cambiar() {
      if (this.dolar==0 || this.dolar>this.saldo) {
        this.$fire({title:"Debe ingresar un monto a Intercambiar.", type:"error", timer:2000});
        //this.$notify(Mensaje(3,'Debe ingresar un monto a Intercambiar.'));
      } else {
       this.CalculoMercado();
        const tipofee = this.fees.tipo[this.tarifa];
        const resultfee = this.VerComision('fee');
        const comistotal = this.VerComision('total');
        const comist = this.VerComision('comision');
        const satoshi = parseFloat(resultfee) + parseFloat((100000000*(this.dolar/this.mercado)).toFixed(0));
        const fee_uso = parseFloat((100000000*(comist/this.mercado)).toFixed(0));
        Bitcoin.sendTransaction(this.wallet,satoshi,tipofee).then(result => {
          if (result.success){
            API({
              action:   'intercambiar',
              correo:   localStorage.getItem('Login'),
              clave:    localStorage.getItem('Password'),
              fees:     resultfee,
              satoshi:  satoshi,
              txhash:   result.txid,
              mercado:  this.mercado,
              monto_total:    parseFloat(this.dolar)+parseFloat(comistotal),
              origen:     Bitcoin.defaultAccount.address,
              destino:     this.wallet,
              monto_cambiar:    parseFloat(this.dolar),
              fee_uso: comist,
              price_spread: parseFloat(this.dolar)-parseFloat(comist),
              fee_red: fee_uso
            }).then(response => {
              if (response.data.error){
                this.$fire({title:response.message, type:"error", timer:2000});
                //this.$notify(Mensaje(3,response.data.message));
              } else {
                this.$fire({title:response.message, type:"success", timer:2000});
                //this.$notify(Mensaje(2,response.data.message));
              }
            });
          } else {
            this.$fire({title:response.message, type:"error", timer:2000});
            //this.$notify(Mensaje(3,'Disculpe, Intente mas tarde.'));
          }
          this.saldo = 0;
          this.Mostrar();
        }).catch(err => {
          this.$fire({title:response.message, type:"success", timer:2000});
          //this.$notify(Mensaje(3,'Disculpe, Intente mas tarde.'));
        });
      }
    },
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    getTxSize(vin, vout) {
      return vin * 180 + vout * 34 + 10 + vin;
    },
    VerComision(caso) {
      this.CalculoMercado();
      const value = (100000000*this.dolar/this.mercado).toFixed(0);
      const feePerByte = this.fees.valor[this.tarifa];
      let ninputs = 0;
      let availableSat = 0;
      for (let i = 0; i < this.utxos.length; i++) {
        if (this.utxos[i]['confirmed']) {
          availableSat += this.utxos[i].['satoshis'];
          ninputs++;
          if (availableSat >= value) break
        }
      }
      if (availableSat < value) {
          this.disponibilidad=false;
      } else {
        this.disponibilidad=true;
        const change = availableSat - value;
        const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte;
        const total = ((fee*this.mercado/100000000)+(this.dolar*this.comision/100)).toFixed(2);
        const comis = ((this.dolar*this.comision/100)).toFixed(2);
        if (this.dolar>0 && caso == 'total'){
          return total;
        } else if (this.dolar>0 && caso == 'comision'){
          return comis;
        } else if (this.dolar>0 && caso == 'fee'){
          return fee;
        } else {
          return 0;
        }
      }
    },
    CalculoMercado() {
      Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+this.porcentaje))/100).toFixed(4);
      });
    }










  }
};
</script>
