let tip = ["info", "primary", "warning", "danger"];
let icono = ["check", "info", "add_alert", "add_alert"];

function notifyVue(tipo, mensaje) {
  const parametros = {
    message: mensaje,
    icon: icono[tipo],
    horizontalAlign: "right",
    verticalAlign: "bottom",
    type: tip[tipo]
  };
  return parametros;
}

export default (tipo, mensaje) => {
  const instance = notifyVue(tipo, mensaje);
  return instance;
};
