import Vue from "vue";
import Vuex from "vuex";
import API from "../service/ApiConsulta";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    error: null,
    message: null,
    login: null,
    usuario: null,
    estatus: false,
    authenticated: false,
    rol: 0,
  },
  mutations: {
    Estatus(state){
      const Consulta = new API();
      Consulta.Estatus().then(response => {
         this.state.estatus = response.estatus;	
      });    
    },
    Seguridad(state,valores){
      this.state.authenticated = true;
      localStorage.removeItem('Login');
      localStorage.removeItem('Password');
      localStorage.setItem('Login', valores.correo);
      localStorage.setItem('Password', valores.clave);
      this.state.usuario = valores.usuario;   
      this.state.login = valores.correo;
      
    },
    Menu(state,valor){
      this.state.rol = valor.rol;
    },
    Validar(state){
      if (localStorage.getItem('Login')) {
        const Consulta = new API();
          Consulta.Ini('seguridad').then(response => {
            if (response.authenticated) {
                  this.state.authenticated = response.authenticated;
                  this.state.usuario = response.user.nombre + ' ' + response.user.apellido;
                  this.state.rol = response.rol;
                } else {
                  localStorage.removeItem('Login');
                  localStorage.removeItem('Password');       
              }
          });              
      }
    },
    Revision(state,valores){
      this.state.error= valores;
      return true;
    },
    Logout(state){
      //const Log = new API();
      localStorage.removeItem('Login');
      localStorage.removeItem('Password');
      this.state.authenticated = false;
      this.state.rol = 0;
      this.state.usuario = null;
    },

  },
  actions: {

  },
  modules: {

  }
});
