<template>
  <div class="content">
    <div
      class="md-layout-item md-medium-size-100 md-size-100"
      v-if="!modificar.activo"
    >
      <md-card>
        <md-card-header data-background-color="blue">
          <h4 class="title">Usuarios Registrados</h4>
          <p class="category">Total de Usuario: {{ usuarios.length }}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="usuarios">
            <md-table-row slot="md-table-row" slot-scope="{ item, index }">
              <md-table-cell md-label="N¬∞">{{ index + 1 }}</md-table-cell>
              <md-table-cell md-label="Apellido y Nombre">{{ item.apellido +', '+ item.nombre }}</md-table-cell>
              <md-table-cell md-label="Correo">{{ item.correo }}</md-table-cell>
              <md-table-cell md-label="Rol-Estatus">{{
                roles[item.rol]
              }}</md-table-cell>
              <md-table-cell md-label="Fecha">{{ item.fecha }}</md-table-cell>
              <md-table-cell md-label="Editar">
                <md-list-item @click="Editar(index)">
                  <i class="material-icons">edit</i>
                </md-list-item>
              </md-table-cell>
            </md-table-row>
          </md-table>
        </md-card-content>
      </md-card>
    </div>

    <div class="md-layout" v-else>
      <div class="md-layout-item md-medium-size-100 md-size-100">
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">Modificar Usuario</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Apellido</label>
                  <md-input v-model="modificar.apellido" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Nombre</label>
                  <md-input v-model="modificar.nombre" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Correo</label>
                  <md-input v-model="modificar.correo" type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Rol-Estatus</label>
                  <md-select v-model="modificar.rol">
                    <md-option
                      v-for="(item, index) in roles"
                      :key="index"
                      :value="index"
                    >
                      {{ item }}
                    </md-option>
                  </md-select>
                </md-field>
              </div>
              <div class="md-layout-item md-size-70 md-medium-size-60 text-right"></div>
              <div class="md-layout-item md-size-15 md-medium-size-20 md-small-size-100 text-right">
                <md-list-item @click="Guardar()">
                  <div class="stats"><md-icon>save</md-icon>Guardar</div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-15 md-medium-size-20 md-small-size-100 text-right">
                <md-list-item @click="Cerrar()">
                  <div class="stats"><md-icon>close</md-icon>Cerrar</div>
                </md-list-item>
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";

export default {
  data() {
    return {
      indice: 0,
      usuarios: [],
      modificar: {
        activo: false,
        id: 0,
        apellido: null,
        nombre: null,
        correo: null,
        rol: null
      },
      roles: ["Inactivo", "B√°sico", "Administrador"]
    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      const Consulta = new API();
      Consulta.Ini("usuarios").then(response => {
        this.usuarios = response.usuario;
      });
    },
    Editar(index) {
      this.modificar.activo = true;
      this.modificar.id = this.usuarios[index].id;
      this.modificar.apellido = this.usuarios[index].apellido;
      this.modificar.nombre = this.usuarios[index].nombre;
      this.modificar.correo = this.usuarios[index].correo;
      this.modificar.rol = this.usuarios[index].rol;
    },
    Guardar() {
      const UPD = new API();
      UPD.Procesar("updateusuario",{
        id: this.modificar.id,
        apellido: this.modificar.apellido,
        nombre: this.modificar.nombre,
        correo: this.modificar.correo,
        rol: this.modificar.rol
      }).then(response => {
        if (response.error) {
          this.$fire({title:response.message, type:"error", timer:2000});          
          //this.$notify(Mensaje(3, response.message));
        } else {
          this.$fire({title:response.message, type:"success", timer:2000});
          //  this.$notify(Mensaje(0, response.message));
          this.uusarios = [];
          this.Mostrar();
        }
      });
    },
    Cerrar() {
      this.modificar.activo = false;
      this.modificar.id = 0;
      this.modificar.apellido = null;
      this.modificar.nombre = null;
      this.modificar.correo = null;
      this.modificar.gestor = 0;
      this.modificar.admin = 0;
    }
  }
};
</script>
