const axios = require("axios");
const bitcoin = require("bitcoinjs-lib");
const bip39 = require("bip39js");

const NETWORKS = {
  testnet: {
    string: "test3",
    object: bitcoin.networks.testnet
  },
  mainnet: {
    string: "main",
    object: bitcoin.networks.bitcoin
  }
};

const PROVIDERS = {
  testnet: "https://testnet-api.smartbit.com.au/v1/blockchain/pushtx",
  //testnet: "https://mempool.space/testnet/api/tx",

  mainnet: "https://testnet-api.smartbit.com.au/v1/blockchain/pushtx"

//  testnet: "https://testnet-api.smartbit.com.au/v1/blockchain/pushtx",
  // https://api.blockcypher.com/v1/btc/test3/txs/push        "tx":
  // https://testnet-api.smartbit.com.au/v1/blockchain/pushtx "hex":
  // https://testnet.blockchain.info/pushtx "tx":
};

const EXPLORE = {
  testnet: "https://blockstream.info/testnet/api/",
  mainnet: "https://blockstream.info/api/"
};

const FEES_URI = {
  testnet: "https://mempool.space/testnet/api/v1/fees/recommended",
  mainnet: "https://mempool.space/api/v1/fees/recommended"
};

const CURRENT_PRICE = "https://api.coindesk.com/v1/bpi/currentprice.json";

const ERROR_MESSAGE = {
  defaultAccount: "Must specify a default account to use eg. keypair",
  validAddress: "The address provided is not a valid one",
  recipient: "Must specify a recipient for the transaction",
  number: "Must specify a proper number"
};

class BTC {
  constructor(network) {
    this.bitcoin = bitcoin;
    this.network = network || "testnet";
    this.defaultAccount = undefined;
    return this.network;
  }

  mnemonicToM (mnemonic, password, p2wpkh) {
    const seed = bip39.mnemonicToSeedSync(mnemonic, password || "");
    const root = bitcoin.bip32.fromSeed(seed, NETWORKS[this.network].object);
    const account = root.derivePath("m/44'/0'/0'");
    const accountXPub = account.neutered().toBase58(); 
    const index = 0; // whatever
    const webRoot = bitcoin.bip32.fromBase58(accountXPub, NETWORKS[this.network].object);
    const webserverChild = webRoot.derive(0).derive(index);
    const APIChild = account.derive(0).derive(index);
    if (p2wpkh == 0) {
      const p2wpkh = bitcoin.payments.p2wpkh({
        pubkey: webserverChild.publicKey ,
        network: NETWORKS[this.network].object
      }); 
      const key = { pubkey: p2wpkh.address , prvkey: APIChild.toWIF()};
      return key;  
    } else if (p2wpkh == 1) {
      const payment = bitcoin.payments.p2wpkh({
        pubkey: webserverChild.publicKey ,
        network: NETWORKS[this.network].object
      }); 
      const p2sh = bitcoin.payments.p2sh({ redeem: payment, network: NETWORKS[this.network].object }); 
      const key = { pubkey: p2sh.address , prvkey: APIChild.toWIF()};
      return key;  
    } else {
      const payment = bitcoin.payments.p2pkh({
        pubkey: webserverChild.publicKey ,
        network: NETWORKS[this.network].object
      });
      const key = { pubkey: payment.address , prvkey: APIChild.toWIF()};
      return key;  
    }
  }

  estimateFee(tipofee) {
    const urifee = `${FEES_URI[this.network]}`;

    if (tipofee == null) {
      return axios.get(urifee).then(r => r.data);
    } else {
      return axios.get(urifee).then(r => r.data[tipofee]);
    }
  }

  currentprice() {
    return axios.get(CURRENT_PRICE).then(r => r.data);
  }

  privateKeyToAccount(privateKey, tipo) {
    let keyPair;
    try {
      keyPair = this.bitcoin.ECPair.fromWIF(
        privateKey,
        NETWORKS[this.network].object
      );
    } catch (e) {
      console.log(e);
      throw ERROR_MESSAGE.validAddress;
    }

    if (tipo == 0) {
      let p2wpkh;
      p2wpkh = bitcoin.payments.p2wpkh({
        pubkey: keyPair.publicKey ,
        network: NETWORKS[this.network].object
      }); 
      const { address } = p2wpkh;
      this.defaultAccount = Object.seal({
        address,
        privateKey,
        keyPair,
        p2wpkh,
        tipo
      });
    } else if (tipo == 1) {
        const payment = bitcoin.payments.p2wpkh({
          pubkey: keyPair.publicKey ,
          network: NETWORKS[this.network].object
        }); 
        let p2sh; 
        p2sh = bitcoin.payments.p2sh({ redeem: payment,
          network: NETWORKS[this.network].object });
  
        const { address } = p2sh;
        this.defaultAccount = Object.seal({
          address,
          privateKey,
          keyPair,
          p2sh,
          tipo
        });
      } else {
      const { address } = bitcoin.payments.p2pkh({
        pubkey: keyPair.publicKey,
        network: NETWORKS[this.network].object
      });
       this.defaultAccount = Object.seal({
        address,
        privateKey,
        keyPair,
        tipo
      });
     }

  }

  isValidWIF(privateKey) {
    try {
      this.bitcoin.ECPair.fromWIF(privateKey, NETWORKS[this.network].object);
      return true;
    } catch (e) {
      return false;
    }
  }

  isValidAddress(address) {
    try {
      this.bitcoin.address.toOutputScript(
        address,
        NETWORKS[this.network].object
      );
      return true;
    } catch (e) {
      return false;
    }
  }

  // If you want to set a conservative limit, 181 bytes per input + 34 bytes per output + 10 bytes seems like a good bet
  // https://bitcointalk.org/index.php?topic=124302.msg1336553#msg1336553
  getTxSize(vin, vout) {
    return vin * 180 + vout * 34 + 10 + vin;
  }

  getBalance(address) {
    if (address == null) {
      address = this.defaultAccount.address;
    }
    if (!this.isValidAddress(address)) throw ERROR_MESSAGE.validAddress;
    const uri = `${EXPLORE[this.network]}address/${address}`;
    return axios.get(uri).then(r => r.data);
  }

  getUtxo(address) {
    if (address == null) {
      address = this.defaultAccount.address;
    }
    if (!this.isValidAddress(address)) throw ERROR_MESSAGE.validAddress;

    const uri = `${EXPLORE[this.network]}address/${address}/utxo`;
    //return axios.get(uri).then(r => r.data)
    return axios.get(uri).then(r =>
      r.data.map(tx =>
        Object.seal({
          txid: tx.txid,
          vout: tx.vout,
          satoshis: tx.value,
          confirmed: tx.status["confirmed"]
        })
      )
    );
  }

  getTxs(address) {
    if (address == null) {
      address = this.defaultAccount.address;
    }
    if (!this.isValidAddress(address)) throw ERROR_MESSAGE.validAddress;
    const uri = `${EXPLORE[this.network]}address/${address}/txs`;
    return axios.get(uri).then(r => r.data);
  }

  getTxid(Tx) {
    const uri = `${EXPLORE[this.network]}tx/${Tx}`;
    return axios.get(uri).then(r => r.data);
  }

  gethex(Tx) {
    const uri = `${EXPLORE[this.network]}tx/${Tx}/hex`;
    return axios.get(uri).then(r => r.data);
  }


  previousTx (address) {
    if (address == null) {
      address = this.defaultAccount.address;
    }
    if (!this.isValidAddress(address)) throw ERROR_MESSAGE.validAddress;

    return Promise.all([
      this.getUtxo(address)
    ]).then(res => {
      let tx = [];
      res[0].forEach(element => {
        const uri = `${EXPLORE[this.network]}tx/${element.txid}/hex`;
        axios.get(uri).then(result =>{
          let utxo = bitcoin.Transaction.fromHex(result.data);
          tx.push({
            tx: result.data,
            txid: utxo.getId(),
            index: element.vout,
            nonWitnessUtxo: Buffer.from(result.data, 'hex'),
            satoshis: element.satoshis,
            confirmed: element.confirmed
          });
        });
      });
      return tx;
    });
  }

//MULTIPLES DE UNO A VARIOS


sendTransaction(to_value, tipofee, dryRun) {
  dryRun = dryRun || false;
  if (!this.defaultAccount) throw ERROR_MESSAGE.defaultAccount;
  let tot_value = 0;
  for (let i = 0; i < to_value.length; i++) {
    tot_value += to_value[i]["value"];
    if (!this.isValidAddress(to_value[i]["to"]))
      throw ERROR_MESSAGE.validAddress;
    if (!to_value[i]["value"] || isNaN(to_value[i]["value"]))
      throw ERROR_MESSAGE.number;
    if (!this.isValidAddress(to_value[i]["to"])) break;
    if (!to_value[i]["value"] || isNaN(to_value[i]["value"])) break;
  }
  if (this.defaultAccount.tipo == 0) {
    //CASO Bech32
    return Promise.all([
      this.estimateFee(tipofee),
      this.previousTx(this.defaultAccount.address)
    ]).then(res => {
      const feePerByte = res[0];
      const utxos = res[1];
      const keypair = this.defaultAccount.keyPair;
      const p2wpkh = this.defaultAccount.p2wpkh;
      const psbt = new bitcoin.Psbt({ network: NETWORKS[this.network].object });

      let ninputs = 0;
      let availableSat = 0;

      for (let i = 0; i < utxos.length; i++) {
        const utxo = utxos[i];
        if (utxo.confirmed) {
          psbt.addInput({
            hash: utxo.txid,
            index: utxo.index,
            nonWitnessUtxo: utxo.nonWitnessUtxo,
          });
          availableSat += utxo.satoshis;
          ninputs++;
          if (availableSat >= tot_value) break;
        }
      }
      if (availableSat < tot_value)
        throw "No tienes suficiente en tu billetera Bech32";
      const change = availableSat - tot_value;
      const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte;

      if (fee > tot_value) throw "Los Satoshis debe ser mayor que la tarifa";

      psbt.addOutput({
        address: to_value[0]["to"],
        value: (to_value[0]["value"] - fee)
      });

      for (let i = 1; i < to_value.length; i++) {
        psbt.addOutput({
          address: to_value[i]["to"],
          value: to_value[i]["value"]
        });
      }

      if (change > 0)  
      psbt.addOutput({
        address: this.defaultAccount.address,
        value: change
      });

      psbt.setMaximumFeeRate(fee); // ignore maxFeeRate we're testnet anyway.
      psbt.setVersion(2);

      const newTx = psbt.__CACHE.__TX.toHex();
   
      for (let i = 0; i < ninputs; i++) {
        psbt.signInput(i, keypair);
      }

      const validated = psbt.validateSignaturesOfInput(0);

      psbt.finalizeAllInputs();
      const hexTx = psbt.extractTransaction().toHex();

      if (dryRun) {
        return Promise.resolve(hexTx);
      } else {
          //const body = { tx: hexTx }
          const body = { hex: hexTx };

          const uri = `${PROVIDERS[this.network]}`;

          //return axios.post(uri, body).then(r => r.data.tx)
          return axios.post(uri, body).then(r => r.data);
      }

    });

  } else if (this.defaultAccount.tipo == 1) {
    //CASO P2SH
    return Promise.all([
      this.estimateFee(tipofee),
      //this.getUtxo(this.defaultAccount.address),
      this.previousTx(this.defaultAccount.address)
    ]).then(res => {
      const feePerByte = res[0];
      const utxos = res[1];
      const keypair = this.defaultAccount.keyPair;
      const p2sh = this.defaultAccount.p2sh;
      const redeemScript = p2sh.redeem.output;
      const psbt = new bitcoin.Psbt({ network: NETWORKS[this.network].object });

      let ninputs = 0;
      let availableSat = 0;

      for (let i = 0; i < utxos.length; i++) {
        const utxo = utxos[i];
        if (utxo.confirmed) {
          psbt.addInput({
            hash: utxo.txid,
            index: utxo.index,
            nonWitnessUtxo: utxo.nonWitnessUtxo,
            redeemScript,
          });
          availableSat += utxo.satoshis;
          ninputs++;
          if (availableSat >= tot_value) break;
        }
      }
      if (availableSat < tot_value)
        throw "No tienes suficiente en tu billetera P2SH";

      const change = availableSat - tot_value;
      const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte;

      if (fee > tot_value) throw "Los Satoshis debe ser mayor que la tarifa";

      psbt.addOutput({
        address: to_value[0]["to"],
        value: (to_value[0]["value"] - fee)
      });

      for (let i = 1; i < to_value.length; i++) {
        psbt.addOutput({
          address: to_value[i]["to"],
          value: to_value[i]["value"]
        });
      }

      if (change > 0)  
      psbt.addOutput({
        address: this.defaultAccount.address,
        value: change
      });

      psbt.setMaximumFeeRate(fee); // ignore maxFeeRate we're testnet anyway.
      psbt.setVersion(2);

      const newTx = psbt.__CACHE.__TX.toHex();
   
      for (let i = 0; i < ninputs; i++) {
        psbt.signInput(i, keypair);
      }

      const validated = psbt.validateSignaturesOfInput(0);

      psbt.finalizeAllInputs();
      const hexTx = psbt.extractTransaction().toHex();

      if (dryRun) {
        return Promise.resolve(hexTx);
      } else {
          //const body = { tx: hexTx }
          const body = { hex: hexTx };

          const uri = `${PROVIDERS[this.network]}`;

          //return axios.post(uri, body).then(r => r.data.tx)
          return axios.post(uri, body).then(r => r.data);
      }
    });

  } else {
    //CASO P2PKH
    let value = 0
    for (let i = 0; i < to_value.length; i++) {
      value += to_value[i]["value"];
    }

    return Promise.all([
      this.estimateFee(tipofee),
      this.getUtxo(this.defaultAccount.address)
    ]).then((res) => {
      const feePerByte = res[0]
      const utxos = res[1]

      //Setup inputs from utxos
      const tx = new this.bitcoin.TransactionBuilder(NETWORKS[this.network].object)
      let ninputs = 0
      let availableSat = 0

      for (let i = 0; i < utxos.length; i++) {
          const utxo = utxos[i]
          if (utxo.confirmed) {
            tx.addInput(utxo.txid, utxo.vout)
            availableSat += utxo.satoshis
            ninputs++        
            if (availableSat >= value) break
          }
      }

      if (availableSat < value) throw "You do not have enough in your wallet"

      const change = availableSat - value
      const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte

      if (fee > value) throw "Satoshis amount must be larger than the fee"


      tx.addOutput(to_value[0]["to"], (to_value[0]["value"] - fee))
      
      for (let i = 1; i < to_value.length; i++) {
        tx.addOutput(to_value[i]["to"], (to_value[i]["value"]))
      }

      if (change > 0) tx.addOutput(this.defaultAccount.address, change)

      for (let i = 0; i < ninputs; i++) {
          tx.sign(i, this.defaultAccount.keyPair)
      }
        
      const hexTx = tx.build().toHex()

      if (dryRun) {
          return Promise.resolve(hexTx)
      } else {
          //const body = { tx: hexTx }
          const body = { hex: hexTx }
          
          const uri = `${PROVIDERS[this.network]}`

          //return axios.post(uri, body).then(r => r.data.tx)
          return axios.post(uri, body).then(r => r.data)  
      }

  })


  }


}


}

export default BTC;
